import React, {memo, FC} from 'react';
import {CreatePostElement} from "../CreatePost/CreatePostElement";
import styles from '../../../styles/MainPart.module.scss';
import {PageId} from "../../../helper/GetPage";
import {PostFeed} from "./PostFeed";
import {USERID} from "../../../USERID";

export const PostZone: FC = memo (() => {

    return (
        <div className={styles.PostZone}>
            {+USERID === PageId
                ?
                    <CreatePostElement/>
                :
                    <></>
            }
            <PostFeed/>
        </div>
    );

});