export interface Comment {
    id: number;
    parent_post_id: number;
    data: string;
    date: string;
    parent_user_id: number;
    likes: number;
    liked_by: number[];
    dislikes: number;
    disliked_by: number[];
    files: string[];
    parent_comment_id: number;
}

export interface CommentDTO{
    ParentCommentId: number;
    Data: string;
    Files: string[];
}

export interface Survey {
    parent_post_id: number;
    data: string[];
    sl0v: number;
    sl0vby: number[];
    sl1v: number;
    sl1vby: number[];
    sl2v: number;
    sl2vby: number[];
    sl3v: number;
    sl3vby: number[];
    sl4v: number;
    sl4vby: number[];
    sl5v: number;
    sl5vby: number[];
    sl6v: number;
    sl6vby: number[];
    sl7v: number;
    sl7vby: number[];
    sl8v: number;
    sl8vby: number[];
    sl9v: number;
    sl9vby: number[];
    votedBy: number[];
    background: string;
    is_multi_voices: boolean;
}

export interface CreateASurveyDTO {
    Data: string[];
    Background: string
    is_multi_voices: boolean;
}

interface PostFields {
    id: number;
    parentUserID: number;
    likes: number;
    liked_by: number[];
    dislikes: number;
    disliked_by: number[];
    data: string;
    date: string;
    files: string[];

}

export interface PostFromServer extends PostFields{
    have_survey: boolean;
}

export interface Post extends PostFields {
    survey: Survey | null | "";
    haveASurvey: boolean;
}

export function createPostFromServer(PostFromServer: PostFromServer, survey: Survey | ""): Post {
    return {survey: survey, haveASurvey: survey != "", ...PostFromServer}
}

export interface CreateAPostDTO {
    Data: string;
    Files: string[];
    HaveASurvey: boolean;
}


