import React, {FC, memo} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {LazyImg} from "../../../helper/LazyImg";
import {PageId} from "../../../helper/GetPage";
import {DOMAIN} from "../../../helper/api";

interface Props {
    ImgStyles?: React.CSSProperties;
    BlockStyles?: React.CSSProperties;
    Src: string;
}

export const Avatar: FC<Props> = memo (({ImgStyles, BlockStyles, Src=''}) => {

    try {
        return (
            <div style={{...BlockStyles}} className={styles.Avatar}>
                {Src == ''
                    ?
                        <LazyImg ImageStyles={ImgStyles} src={DOMAIN + '/UserFiles/' +'NULL.png'}/>
                    :
                        <LazyImg ImageStyles={ImgStyles} src={DOMAIN + '/UserFiles/' +PageId+'/Image/' + Src}/>
                }
            </div>
        );
    } catch (e) {
        return (<></>);
    }

});