import {action, observable, runInAction} from "mobx";
import {InfoStore} from "./InfoStore";
import {PageId} from "../helper/GetPage";
import {USERID} from "../USERID";
import {hand, RestApiMethods} from "../helper/hand";

interface FriendStatusStoreInterface {
	urOwnerSubs: number[];
	StateType: 'none' | 'friend' | 'subscriber' | 'idol' | 'first';

	readonly acceptTheInvitation: () => void;
	readonly getIsFriendsState: () => void;
	readonly sendAFriendshipRequest: () => void;
	readonly removeFromFriendList: () => void;
	readonly takeBackFriendShipRequest: () => void;
}

export const FriendStatusStore = observable<FriendStatusStoreInterface>({
	StateType: 'first',
	urOwnerSubs: [],

	acceptTheInvitation: action(() => {
		hand('/api/user/friends/add/' + PageId, RestApiMethods.Patch)
			.then (response => {
				if (response?.status === 204) {
					runInAction(() => {FriendStatusStore.StateType = "friend"})
				}
			})
	}),

	sendAFriendshipRequest: action(() => {
		hand('/api/user/subscribers/add/' + PageId, RestApiMethods.Patch)
			.then (response => {
				if (response?.status === 204) {
					runInAction(() => {FriendStatusStore.StateType = "idol"})
				}
			})
	}),

	removeFromFriendList: action(() => {
		hand('/api/user/friends/delete/' + PageId, RestApiMethods.Patch)
			.then (response => {
				if (response?.status === 204) {
					runInAction(() => {FriendStatusStore.StateType = "subscriber"})
				}
			})
	}),

	takeBackFriendShipRequest: action(() => {
		hand('/api/user/subscribers/delete/' + PageId, RestApiMethods.Patch)
			.then (response => {
				if (response?.status === 204) {
					runInAction(() => {FriendStatusStore.StateType = "none"})
				}
			})
	}),

	getIsFriendsState: action(() => {
		if (InfoStore.friends.indexOf(+USERID) != -1) {
			FriendStatusStore.StateType = "friend"
			return;
		}
		if (InfoStore.subscribers.indexOf(+USERID) != -1) {
			FriendStatusStore.StateType = "idol"
			return;
		}

		if (FriendStatusStore.urOwnerSubs != null) {
			if (FriendStatusStore.urOwnerSubs.indexOf(+USERID)!= -1) {
				FriendStatusStore.StateType = "subscriber"
				return;
			}
		}

		FriendStatusStore.StateType = "none"
	}),
})