import React, {memo, FC, forwardRef, useCallback, ChangeEvent} from 'react';
import styles from '../../../../styles/MainPart.module.scss';
import {AlreadyInput} from "../../../../helper/AlreadyInput";

export const SurveyLine: FC<{label: string, ref: any, onClick?: any, onChange: any, number: number, withCross?: boolean, value: string}> = memo (forwardRef(({value, label,withCross = false, onClick, onChange, number}, ref) => {

    const onChange1 = useCallback ((event: ChangeEvent<HTMLInputElement>) => {
        onChange(number, event.target.value);
    }, []);

    const OnCLick = useCallback (() => {
        onClick(number);
    }, []);

    return (
        <>
            {withCross
                ?/*@ts-ignore*/
                    <AlreadyInput onChange={onChange1} width={'calc(100% - 50px)'} reg={'all'} length={128} label={label} value={value} ref={ref} design={'cross'} onClick={OnCLick} ClassName={styles.line}/>
                :/*@ts-ignore*/
                    <AlreadyInput onChange={onChange1} width={'calc(100% - 18px)'} reg={'all'} design={'default'} length={128} value={value} label={label} ref={ref} ClassName={styles.line}/>
            }
        </>
    );

}) );