import React, {memo, FC, useRef} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {InfoStore} from "../../../store/InfoStore";

interface Props {
    SetIsAvatarChange: Function;
}

export const ChangeAvatar: FC<Props> = memo (({SetIsAvatarChange}) => {

    const Input = useRef<HTMLInputElement>(null);

    const ChooseAvatar = () => {
        ( Input.current as HTMLInputElement).click();
    }, UploadAvatar = (File:File) => {
        SetIsAvatarChange(false);
        InfoStore.changeAvatar(File);
    }
    return (
        <div className={styles.ChangeAvatarWindow} /*region*/ onClick={
            () => {
                ChooseAvatar();
            }
        }
        onDragStart = {e => {e.preventDefault();} }
        onDragLeave = {e => {e.preventDefault();} }
        onDragOver = {e => {e.preventDefault();} }
        onDrop={
            (event) => {
                event.stopPropagation();
                event.preventDefault();
                UploadAvatar(event.dataTransfer.files[0])
            }
        }
        /*endregion*/>
            <input style={{display: 'none'}} ref={Input} type={'file'} onChange={e => { // @ts-ignore
                UploadAvatar( Input.current.files[0] ) } }/>
            <div className={styles.ChangeAvatarWindow_Cross} onClick={
                (event) => {
                    event.stopPropagation();
                    SetIsAvatarChange(false);
                }
            }>
                ×
            </div>
            <div className={styles.ChangeAvatarWindow_Text}>
                Перетащите сюда ваш новый аватар или нажмите здесь
            </div>
        </div>
    );

});