import React, {memo, FC, useCallback} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {DOMAIN} from "../../../helper/api";
import {ImageStore} from "../../../LayOut/store/stores/ImageStore";
import {USERID} from "../../../USERID";

export const FileBlockImage: FC<{Images: string[] } > = memo(({Images}) => {

    const FirstArray = [];
    const SecondArray = [];
    const ThirdArray = [];
    const openImage = useCallback( (number: number) => {
        ImageStore.openNewImage(Images[number], DOMAIN + '/UserFiles/' + USERID + '/Image/' + Images[number]);
    }, [Images]);

    if (Images.length > 0) {
        for (let i = 0; i < Images.length && i < 4;++i) {
            FirstArray.push(Images[i]);
        }
        for (let i = 4; i < Images.length && i < 8;++i) {
            SecondArray.push(Images[i]);
        }
        for (let i = 8; i< Images.length && i < 10; ++i) {
            ThirdArray.push(Images[i])
        }
    }

    return (
        <div className={styles.ImageBlockInCreate}>
            {
                FirstArray.length > 0
                    ?
                        <div className={styles.subblock}>
                            {
                                FirstArray.map ((Image, Index) => {
                                    return (
                                        <div onClick={() => {openImage(Index) } } key={Index} className={styles.ImgBlock}>
                                            <img src={DOMAIN + '/UserFiles/' + USERID+'/Image/' + Image} alt={''}/>
                                            <div className={styles.text}>
                                                {Image}
                                            </div>
                                        </div>
                                    );
                                })
                            }
                        </div>
                    :
                        <></>
            }
            {
                SecondArray.length > 0
                    ?
                        <div className={styles.subblock}>
                            {
                                SecondArray.map ((Image, Index) => {
                                    return (
                                        <div onClick={() => {openImage(Index) } } key={Index} className={styles.ImgBlock}>
                                            <img src={DOMAIN + '/UserFiles/'+USERID+'/Image/' + Image} alt={''}/>
                                            <div className={styles.text}>
                                                {Image}
                                            </div>
                                        </div>
                                    );
                                })
                            }
                        </div>
                    :
                        <></>
            }
            {
                ThirdArray.length > 0
                    ?
                        <div className={styles.subblock}>
                        {
                            ThirdArray.map ((Image, Index) => {
                                return (
                                    <div onClick={() => {openImage(Index) } } key={Index} className={styles.ImgBlock}>
                                        <img src={DOMAIN + '/UserFiles/' + USERID+'/Image/' + Image} alt={''}/>
                                        <div className={styles.text}>
                                            {Image}
                                        </div>
                                    </div>
                                );
                            })
                        }
                    </div>
                    :
                        <></>
            }
        </div>
    );

});