import React, {FC, useState} from 'react';
import {ProfileTitle} from "./ProfileTitle";
import styles from '../../../styles/MainPart.module.scss';
import {ProfileInfoBlock} from "./ProfileInfoBlock";
import {QuestionLine} from "./QuestionLine";
import { InfoStore } from '../../../store/InfoStore';
import {FriendStatusStore} from "../../../store/FriendStatusStore";
import {observer} from "mobx-react-lite";

export const ProfileInfo: FC = observer(() => {

    const /**Is detailed info is empty*/
      IsProfileEmpty = (
          InfoStore.dreams == null && InfoStore.attitude_to_alcohol == null && InfoStore.attitude_to_smocking == null
          && InfoStore.attitude_to_sport == null && InfoStore.family_status == null && InfoStore.birthday == null
          && InfoStore.favourites_books == null && InfoStore.favourites_films == null && InfoStore.favourites_games == null
          && InfoStore.favourites_meals == null
      ),
      /**Are we showing all info*/[IsShowingAllInfo, SetIsShowingAllInfo] = useState(false);

    if (InfoStore.name === null || FriendStatusStore.StateType === 'first') {
        return (
            <>
                <div className={'PrevProfileInfo'} style={{width: '525px', marginBottom: '6px', top: '4px', left: '258px'}}>
                    <div className="PrevUserTitle" style={{position: 'relative'}}>
                        <div className="PrevUserName"></div>
                        <div className="PrevUserSurname" style={{left: '3px'}}></div>
                        <div className="PrevUserDescription"></div>
                    </div>
                    <div className="PrevUserInfo_Info">
                        <div className="PrevUserInfo_Info__Block">
                            <div className="PrevUserInfo_Info__Right"></div>
                            <div className="PrevUserInfo_Info__Left" style={{left: '8px'}}></div>
                        </div>
                        <div className="PrevUserInfo_Info__Block">
                            <div className="PrevUserInfo_Info__Right"></div>
                            <div className="PrevUserInfo_Info__Left" style={{left: '8px'}}></div>
                        </div>
                        <div className="PrevUserInfo_Info__Block">
                            <div className="PrevUserInfo_Info__Right"></div>
                            <div className="PrevUserInfo_Info__Left" style={{left: '8px'}}></div>
                        </div>
                    </div>
                    <div className="PrevUserInfo__Button"></div>
                </div>
            </>
        );
    }

    return (
        <div className={styles.ProfileInfo}>
            <ProfileTitle/>
            <ProfileInfoBlock/>
            {IsShowingAllInfo
                ?
                    <>
                        <div className={styles.DetailedInfo}>
                            <QuestionLine id={6} Question={'Любимая еда'} Content={InfoStore.favourites_meals}/>
                            <QuestionLine id={7} Question={'Любимые фильмы и сериалы'} Content={InfoStore.favourites_films}/>
                            <QuestionLine id={8} Question={'Любимые книги'} Content={InfoStore.favourites_books}/>
                            <QuestionLine id={9} Question={'Любимые игры'} Content={InfoStore.favourites_games}/>
                            <QuestionLine id={10} Question={'Мечта'} Content={InfoStore.dreams}/>
                            <QuestionLine id={11} Question={'Отношение к алкоголю'} Content={InfoStore.attitude_to_alcohol}/>
                            <QuestionLine id={12} Question={'Отношение к курению'} Content={InfoStore.attitude_to_smocking}/>
                            <QuestionLine id={13} Question={'Отношение к спорту'} Content={InfoStore.attitude_to_sport}/>
                        </div>
                        <div className={styles.Button} style={{left: '187px', width: '148px'}} onClick={ () => {SetIsShowingAllInfo(false)} }>
                            Показать меньше
                        </div>
                    </>
                :
                    !IsProfileEmpty
                    ?
                        <div className={styles.Button} style={{left: '189px', width: '146px'}} onClick={ () => {SetIsShowingAllInfo(true)} }>
                            Показать больше
                        </div>
                    :
                        <></>
            }
        </div>
    );

});