import React, {memo, FC} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {QuestionLine} from "./QuestionLine";
import {InfoStore} from "../../../store/InfoStore";
import {observer} from "mobx-react-lite";

export const ProfileInfoBlock: FC = observer(() => {

    if (InfoStore.dreams == null && InfoStore.attitude_to_alcohol == null && InfoStore.attitude_to_smocking == null
        && InfoStore.attitude_to_sport == null && InfoStore.family_status == null && InfoStore.birthday == null && InfoStore.favourites_books == null
        && InfoStore.favourites_films == null && InfoStore.favourites_games == null && InfoStore.favourites_meals == null && InfoStore.place_of_residence == null) {
        return (<></>)
    }

    return (
        <div className={styles.ProfileInfoBlock}>
            <QuestionLine id={3} Question={'День рождения'} Content={InfoStore.birthday}/>
            <QuestionLine id={4} Question={'Место проживания'} Content={InfoStore.place_of_residence}/>
            <QuestionLine id={5} Question={'Семейное положение'} Content={InfoStore.family_status}/>
        </div>
    );

});