import React, {memo, FC} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {DOMAIN} from "../../../helper/api";
import {USERID} from "../../../USERID";

export const FileBlockOther: FC<{OtherList: string[] }> = memo (({OtherList}) => {

    return (
        <div className={styles.OtherBlockInCreate}>
            {
                OtherList.map( (File, Index) => {
                    return (
                        <a download key={Index} href={DOMAIN + '/UserFiles/' + USERID + '/Other/' + File}>
                            {File}
                        </a>
                    );
                })
            }
        </div>
    );

});