import React, {memo, FC} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {TextBlock} from "./TextBlock";
import {InfoStore} from "../../../store/InfoStore";
import {observer} from "mobx-react-lite";

export const ProfileTitle: FC = observer(() => {

    return (
        <div className={styles.ProfileTitle}>
            <TextBlock id={'TextBlock1'} Content={InfoStore.name + ' ' + InfoStore.surname} Styles={{width:'520px', fontSize: '23px', backgroundColor: 'rgba(0, 0, 0, 0)' }}/>
            <TextBlock id={'TextBlock2'} Content={InfoStore.description} Styles={{width:'520px',color: 'grey', padding: '2px 0 0 0', backgroundColor: 'rgba(0, 0, 0, 0)' }}/>
        </div>
    );

});