import React, {memo, FC, useState} from 'react';
import styles from '../styles/MainPart.module.scss';
import {LeftColumn} from "./AI/Info/LeftColumn";
import {ChangeMenu} from "./AI/Change/ChangeMenu";
import {ProfileInfo} from "./UI/Info/ProfileInfo";
import {PostZone} from "./AI/Post/PostZone";
import {PageId} from "../helper/GetPage";
import {USERID} from "../USERID";

export const MainPart: FC = memo (() => {

    const [IsChange, SetIsChange] = useState(false);

    return (
        <>
            {IsChange
                ?
                    <div className={styles.MainPart}>
                        <ChangeMenu SetIsChange={SetIsChange}/>
                    </div>
                :
                    <div className={styles.MainPart}>
                        <LeftColumn SetIsChange={SetIsChange} IsOwner={+USERID === PageId}/>
                        <ProfileInfo/>
                        <PostZone/>
                    </div>
            }
        </>
    )
});