//function that accepts Date and returns a Date in format "HH:Minutes DD name of Month YYYY"
export const FormatDate = (date: Date) => `${date.getHours() > 9 ? date.getHours() : `0${date.getHours()}`}:${date.getMinutes() > 9 ? date.getMinutes() : `0${date.getMinutes()}`} ${date.getDay()} ${formatMonth(date.getMonth())} ${date.getFullYear()}`;

//function that accepts a number of Months and return name of month
export const formatMonth = (month: number) => {
    switch (month) {
        case 1:
            return "Января";
        case 2:
            return "Февраля";
        case 3:
            return "Марта";
        case 4:
            return "Апреля";
        case 5:
            return "Мая";
        case 6:
            return "Июня";
        case 7:
            return "Июля";
        case 8:
            return "Августа";
        case 9:
            return "Сентября";
        case 10:
            return "Октября";
        case 11:
            return "Ноября";
        case 12:
            return "Декабря";
    }
};

export const GetDateWithCurrentMonth = (date: string) => {
    const splittedDate = date.split(' ');
    const monthNumber = splittedDate[1];
    let month: string;
    switch (monthNumber) {
        case "01": {
            month = "января";
            break;
        }
        case "02": {
            month = "февраля";
            break;
        }
        case "03": {
            month = "марта";
            break;
        }
        case "04": {
            month = "апреля";
            break;
        }
        case "05": {
            month = "мая";
            break;
        }
        case "06": {
            month = "июня";
            break;
        }
        case "07": {
            month = "июля";
            break;
        }
        case "08": {
            month = "августа";
            break;
        }
        case "09": {
            month = "сентября";
            break;
        }
        case "10": {
            month = "октября";
            break;
        }
        case "11": {
            month = "ноября";
            break;
        }
        case "12": {
            month = "декабря";
            break;
        }
        default: {
            month = "";
            break;
        }
    }
    return `${splittedDate[0]} ${month} ${splittedDate[2]} ${splittedDate[3]} ${splittedDate[4]}`;
}