import {action, observable, runInAction} from "mobx";
import {hand, handConfig, RestApiMethods} from "../helper/hand";
import {ConvertToPQArray} from "../helper/ConvertToPQArray";


export interface User {
	Avatar: string;
	Name: string;
	Surname: string;
	ID: number;
}

export const UsersStore = observable({
	users: [] as User[],

	getUsers: action(async (IdOfUsers: number[]) => {
		const list = ConvertToPQArray(IdOfUsers);
		return hand(`/api/user/many?idOfUsers=${list}`, RestApiMethods.Get)
			.then(res => {
				if (res?.status === 200) {
					return res?.json();
				}
			})
			.then((Res: {users: {Avatar: string, ID : number, Name: string, Surname: string}[]}) => {
				let users: User[] = [];
				for (let i = 0; i < Res.users.length; i++) {
					users.push({Avatar: Res.users[i].Avatar, ID: Res.users[i].ID, Name: Res.users[i].Name, Surname: Res.users[i].Surname});
				}
				runInAction(() => {
					UsersStore.users.push(...users)
				})
				return true;
			})
			.catch(() => {return false});
	})
})