import React, {memo, FC} from 'react';
import {FileBlockAudio} from "../../UI/CreatePost/FileBlock_Audio";
import {FileBlockImage} from "../../UI/CreatePost/FileBlock_Image";
import {FileBlockOther} from "../../UI/CreatePost/FileBlock_Other";
import {FileBlockVideo} from "../../UI/CreatePost/FileBlock_Video";

interface Props {
    VideoList: string[];
    AudioList: string[];
    ImageList: string[];
    OtherList: string[];
}

export const FileBlock: FC<Props> = memo (({VideoList, OtherList, AudioList, ImageList}) => {

    return (
        <div>
            <FileBlockAudio Audios={AudioList}/>
            <FileBlockImage Images={ImageList}/>
            <FileBlockVideo Videos={VideoList}/>
            <FileBlockOther OtherList={OtherList}/>
        </div>
    );

});