import React, {memo, FC, MouseEvent, useState} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {Button} from "@mui/material";

interface Props {
    WhatIsIt: 'clip' | 'survey' | 'eye' | 'create';
    Function: (event: MouseEvent<HTMLDivElement>) => void;
    SetHint?: Function;
}

export const CreatePostButton: FC<Props> = memo (({WhatIsIt, Function, SetHint}) => {

    const [IsShowingHint, setIsShowingHint] = useState(false);

    function ShowHint() {
        if (WhatIsIt === 'create') return;
        setIsShowingHint(true);
        if (SetHint)
        SetHint(true);
    }

    function HideHind() {
        if (WhatIsIt === 'create') return;
        setIsShowingHint(false);
        if (SetHint)
        SetHint(false);
    }

    return (
        <div className={styles.CreatePostButton} onMouseEnter={ShowHint}
             onMouseLeave={HideHind} onClick={Function}>
            {WhatIsIt === 'clip'
                ?
                    <div style={{transform: 'rotateZ(90deg)'}}>
                        <svg width="28px" height="28px" viewBox="0 0 59 63">
                            <g stroke="none" fill="none">
                                <path
                                    d="M36.3,46.5 L12.1,22.4 C9.4,19.7 9.1,15.6 11.8,12.9 L11.8,12.9 C14.5,10.2 19.1,10.1 21.8,12.8 L53.6,44.4 C57.8,48.6 58.2,54.9 54.5,58.5 L54.5,58.5 C50.8,62.1 44.5,61.7 40.3,57.6 L4.7,22.1 C-0.1,17.4 -0.2,9.8 4.3,5.3 L5.3,4.3 C9.8,-0.2 17.4,-0.1 22.2,4.7 L46,28.4"
                                    stroke="#6B6C6E"></path>
                            </g>
                        </svg>
                    </div>

                :
                    WhatIsIt === 'survey'
                        ?
                            <div onClick={Function}>
                                <svg height="26px" viewBox="0 0 500 500" width="26px" >
                                    <g>
                                        <line fill="none" stroke="#808080" strokeLinecap="round" x1="363" strokeWidth={'10'} strokeMiterlimit="2.6131" strokeLinejoin="round" x2="397" y1="82" y2="82"/>
                                        <path d="   M397.066,82.696c11.988,0,21.689,9.811,21.689,21.939" fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10"/>
                                        <line fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10" x1="418.756" x2="418.756" y1="104.636" y2="453.047"/>
                                        <path d="   M418.756,453.047c0,12.114-9.701,21.953-21.689,21.953" fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10"/>
                                        <line fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10" x1="397.066" x2="102.932" y1="475" y2="475"/>
                                        <path d="   M102.932,475c-11.975,0-21.688-9.839-21.688-21.953" fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10"/>
                                        <line fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10" x1="81.244" x2="81.244" y1="453.047" y2="104.636"/>
                                        <path d="   M81.244,104.636c0-12.128,9.713-21.939,21.688-21.939" fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10"/>
                                        <line fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10" x1="102.932" x2="131.906" y1="82.696" y2="82.696"/>
                                        <polyline fill="none" points="   342.859,50.625 286.991,50.625 286.991,45.851  " stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10"/>
                                        <path d="   M286.991,45.851c0-11.542-9.24-20.851-20.571-20.851" fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10"/>
                                        <line fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10" x1="266.42" x2="233.621" y1="25" y2="25"/>
                                        <path d="   M233.621,25c-11.359,0-20.627,9.309-20.627,20.851" fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10"/>
                                        <polyline fill="none" points="   212.994,45.851 212.994,50.625 157.181,50.625  " stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10"/>
                                        <path d="   M157.181,50.625c-11.374,0-20.599,9.35-20.599,20.878" fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10"/>
                                        <polyline fill="none" points="   136.582,71.503 136.582,115.857 363.459,115.857 363.459,71.503  " stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10"/>
                                        <path d="   M363.459,71.503c0-11.528-9.184-20.878-20.6-20.878" fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10"/>
                                        <g>
                                            <line fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10" x1="137.6" x2="156.218" y1="221.173" y2="238.492"/>]
                                            <line fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10" x1="190.412" x2="156.218" y1="200.699" y2="238.492"/>
                                        </g>
                                        <line fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10" x1="222.875" x2="362.44" y1="223.462" y2="223.462"/>]
                                        <line fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10" x1="222.875" x2="362.44" y1="392.56" y2="392.56"/>
                                        <line fill="none" stroke="#808080" strokeLinecap="round" strokeLinejoin="round" strokeMiterlimit="2.6131" strokeWidth="10" x1="222.875" x2="362.44" y1="309.239" y2="309.239"/>
                                    </g>
                                </svg>
                            </div>
                        :
                            WhatIsIt === 'eye'
                            ?
                                <div>
                                    <svg width={'25px'} height={'25px'} enableBackground="new 0 0 32 32" viewBox="0 0 32 32">
                                        <path fill={'none'}
                                            d="  M16,7C9.934,7,4.798,10.776,3,16c1.798,5.224,6.934,9,13,9s11.202-3.776,13-9C27.202,10.776,22.066,7,16,7z"
                                            stroke="rgb(128, 128, 128)" strokeLinecap="round"
                                            strokeLinejoin="round" strokeMiterlimit="10" strokeWidth="2"/>
                                        <circle cx="16" cy="16" fill="none" r="5" stroke="rgb(128, 128, 128)"
                                                strokeLinecap="round" strokeLinejoin="round"
                                                strokeMiterlimit="10" strokeWidth="2"/>
                                    </svg>
                                </div>
                            :
                                <Button className={'noselect'} style={{ position:'relative', left: '232px', padding: '4px', backgroundColor: 'rgb(255, 255, 255, .3)', color: 'white', borderRadius: '4px'}}>
                                    Опубликовать пост
                                </Button>
            }
            {IsShowingHint
                ?
                    <div className={styles.Hint}>
                        {WhatIsIt === 'clip'
                            ?
                                'Прикрепить файл'
                            :
                                WhatIsIt === 'survey'
                                    ?
                                        'Создать опрос'
                                    :
                                        WhatIsIt === 'eye'
                                        ?
                                            'Указать для кого виден пост'
                                            :
                                                <></>
                        }
                    </div>
                :
                    <></>
            }
        </div>
    );

});