import React, {FC, useRef, useState, useEffect, useCallback} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {SetLoading} from "../../../helper/SetLoading";
import {Post} from "../../UI/Post/Post";
import {PostState} from "../../../store/PostsState";
import {FriendStatusStore} from "../../../store/FriendStatusStore";
import {observer} from "mobx-react-lite";
import {PageId} from "../../../helper/GetPage";
import {SortFilesByExtension} from "../../../helper/SortFilesByExt";

export const PostFeed: FC = observer(() => {

    /** This is a count that need to understand how much music we have in page already (to create ID`s for music)*/
    const countOfMusic = useRef (0);
    // const [deletedList, setDeletedList] = useState<number[]> ([]);
    const [numberOfTwenties, serNumberOfTwenties] = useState (1);
    const isLoading = useRef (false);
    const /*** The last post in this page now. It`s need to upload more post, when user need*/LastPost = useRef<HTMLDivElement>(null),
        observer = useRef<IntersectionObserver>();
    isLoading.current = false;

    // const SetDeletedList = useCallback ((id: number) => {
    //     setDeletedList([...deletedList, id]);
    // }, [deletedList]);

    useEffect (() => {
        if (observer.current) observer.current?.disconnect();

        const callback = (entries: any) => {
            if (!PostState.isThereMore || isLoading.current) return;

            if (!entries[0].isIntersecting) return;

            if ( (numberOfTwenties * 20) < PostState.NumberOfPosts) {serNumberOfTwenties(numberOfTwenties + 1);return;}

            isLoading.current = true;

            PostState.getPosts(PageId, PostState.NumberOfSets)
        }
        if (LastPost.current) {
            observer.current = new IntersectionObserver(callback);
            observer.current.observe(LastPost.current);
        }
    });

    if (FriendStatusStore.StateType === 'first') {
        return <></>;
    }
    if (PostState.NumberOfSets !== 0) {
        return (
            <div className={styles.PostFeed}>
                {
                    PostState.Posts.map( (value, index, array) => {
                        const MusicNumbers:number[] = [];
                        let music_files: any[] = [], image_files: any[] = [], other_files: any[] = [], video_files: any[] = [];
                        if (value.files != null) {
                            // @ts-ignore
                            const {music_files1, image_files1, video_files1, other_files1} = SortFilesByExtension(...value.files);
                            music_files = music_files1;
                            image_files = image_files1;
                            video_files = video_files1;
                            other_files = other_files1;
                            if (music_files != null && music_files.length > 0) {
                                music_files.forEach( (value, index) => {
                                    MusicNumbers.push(countOfMusic.current + index);
                                })
                                countOfMusic.current += music_files.length;
                            }
                        }
                        return(
                            <div key={value.id}>
                                {index === array.length - 1
                                    ?
                                        <div ref={LastPost} style={{width: '100%', height: '1px'}} className={'noselect'}/>
                                    :
                                        <></>
                                }
                                <Post MusicNumbers={MusicNumbers}
                                      IsFriend={FriendStatusStore.StateType === 'friend'} music_files={music_files} image_files={image_files}
                                      other_files={other_files} video_files={video_files} survey={value.survey}
                                      id={value.id} disliked_by={value.disliked_by}
                                      likes={value.likes} dislikes={value.dislikes} date={value.date}
                                      liked_by={value.liked_by}
                                      data={value.data}
                                      files={[]}
                                      haveASurvey={value.haveASurvey}
                                      parentUserID={value.parentUserID}
                                />
                            </div>
                        )
                    })
                }
            </div>
        );
    } else {
         PostState.getPosts(PageId, 0);
         return (
             <div style={{position: 'fixed', top: 'calc(50vh - 60px)', left: 'calc(50vw + 250px)'}}>
                 <SetLoading/>
             </div>
         );
    }
});