import React, {memo, FC} from 'react';
import styles from "../../../styles/MainPart.module.scss";

interface Props {
    SetIsChange?: Function;
}

export const OwnerPanel: FC<Props> = memo (({SetIsChange}) => {

    return (
        <div>
            <div className={"PrevButtonInLeftColumn"}>
                <div className={styles.PrevButtonInLeftColumn_Text} onClick={
                    () => {
                        // @ts-ignore
                        SetIsChange(true);
                    }
                }>Редактировать профиль</div>
            </div>
        </div>
    );

});