import React, {memo, FC, useCallback, useRef, useState} from 'react';
import {InfoInput} from "../../UI/Change/InfoInput";
import styles from "../../../styles/MainPart.module.scss";
import {SetNotification} from "../../../helper/SetNotification";
import {InfoSelect} from "../../UI/Change/InfoSelect";
import {InfoDate} from "../../UI/Change/InfoDate";
import {InfoStore} from "../../../store/InfoStore";
import {observer} from "mobx-react-lite";
import {AlreadyInput} from "../../../helper/AlreadyInput";
import {SelectChangeEvent} from "@mui/material";

interface Props {
    SetIsChange: Function;
}

export const ChangeRight: FC<Props> = observer(({SetIsChange}) => {

    //@ts-ignore
    const name = useRef<HTMLInputElement>(null);//@ts-ignore
    const surname = useRef<HTMLInputElement>(null);//@ts-ignore
    const description = useRef<HTMLInputElement>(null);//@ts-ignore
    const birthday = useRef<HTMLInputElement>(null);
    const [attitudeToAlcohol, setAttitudeToAlcohol] = useState(InfoStore.attitude_to_alcohol !== null ? InfoStore.attitude_to_alcohol : '');
    const [attitudeToSport, setAttitudeToSport] = useState(InfoStore.attitude_to_sport !== null ? InfoStore.attitude_to_sport : '');
    const [attitudeToSmocking, setAttitudeToSmocking] = useState(InfoStore.attitude_to_smocking !== null ? InfoStore.attitude_to_smocking : '');//@ts-ignore
    const favoriteBooks = useRef<HTMLInputElement>(null);//@ts-ignore
    const favoriteFilms = useRef<HTMLInputElement>(null);//@ts-ignore
    const favoriteGames = useRef<HTMLInputElement>(null);//@ts-ignore
    const favoriteMeals = useRef<HTMLInputElement>(null);//@ts-ignore
    const [familyStatus, setFamilyStatus] = useState(InfoStore.family_status !== null ? InfoStore.family_status : '');//@ts-ignore
    const dreams = useRef<HTMLInputElement>(null);//@ts-ignore
    const placeOfResidence = useRef<HTMLInputElement>(null);

    const NameStyles:React.CSSProperties = {
            display: 'inline-block',
        },
        SurnameStyles:React.CSSProperties = {
            display: 'inline-block',
            left: '7px'
        },
        AcceptChanges = useCallback( () => {

        //region Hell
        let NewName = ((name. current as HTMLInputElement).value=== '' ||
            (name. current as HTMLInputElement).value=== null ||
            (name. current as HTMLInputElement).value=== 'null' ||
            (name. current as HTMLInputElement).value=== ''
            || (name. current as HTMLInputElement).value=== 'Ваше имя')
                ?
                    null
                :
                (name. current as HTMLInputElement).value;


        let NewSurname = ((surname. current as HTMLInputElement).value === ''
            ||(surname. current as HTMLInputElement).value === null
            ||(surname. current as HTMLInputElement).value === 'null'
            || (surname. current as HTMLInputElement).value === ''
            || (surname. current as HTMLInputElement).value === 'Ваша фамилия')
            ?
                null
            :
                (surname. current as HTMLInputElement).value;

        let NewBirthday = ( (birthday. current as HTMLInputElement).value === ''
                || (birthday. current as HTMLInputElement).value === null
                || (birthday. current as HTMLInputElement).value === 'null'
                || (birthday. current as HTMLInputElement).value === ''
                || (birthday. current as HTMLInputElement).value === 'День рождения')
                ?
                    null
                :
                    (birthday. current as HTMLInputElement).value;

        let NewFavoriteBooks = ( (favoriteBooks. current as HTMLInputElement).value === ''
                ||(favoriteBooks. current as HTMLInputElement).value === null
                ||(favoriteBooks. current as HTMLInputElement).value === 'null'
                || (favoriteBooks. current as HTMLInputElement).value === ''
                || (favoriteBooks. current as HTMLInputElement).value === 'Любимые книги')
                ?
                    null
                :
                    (favoriteBooks. current as HTMLInputElement).value;//TODO книги копируются в игры и в итоге изменить игры нельзя. фикси!

        let NewFavoriteFilms = ( (favoriteFilms. current as HTMLInputElement).value === ''
                ||(favoriteFilms. current as HTMLInputElement).value === null
                ||(favoriteFilms. current as HTMLInputElement).value === 'null'
                || (favoriteFilms. current as HTMLInputElement).value === ''
                || (favoriteFilms. current as HTMLInputElement).value === 'Любимые фильмы и сериалы')
                ?
                    null
                :
                    (favoriteFilms. current as HTMLInputElement).value;

        let NewFavoriteGames = ( (favoriteGames. current as HTMLInputElement).value === ''
                ||(favoriteGames. current as HTMLInputElement).value === null
                ||(favoriteGames. current as HTMLInputElement).value === 'null'
                || (favoriteGames. current as HTMLInputElement).value === ''
                || (favoriteGames. current as HTMLInputElement).value === 'Любимые игры')
                ?
                    null
                :
                    (favoriteGames. current as HTMLInputElement).value;

        let NewFavoriteMeals = ( (favoriteMeals. current as HTMLInputElement).value === ''
                ||(favoriteMeals. current as HTMLInputElement).value === null
                ||(favoriteMeals. current as HTMLInputElement).value === 'null'
                || (favoriteMeals. current as HTMLInputElement).value === ''
                || (favoriteMeals. current as HTMLInputElement).value === 'Любимая еда')
                ?
                    null
                :
                    (favoriteMeals. current as HTMLInputElement).value;

        let NewDescription = ( (description.current as HTMLInputElement).value === ''
                ||(description.current as HTMLInputElement).value === null
                ||(description.current as HTMLInputElement).value === 'null'
                || (description.current as HTMLInputElement).value === ''
                || (description.current as HTMLInputElement).value === 'О себе')
                ?
                    null
                :
                    (description.current as HTMLInputElement).value;

        let NewFamilyStatus = ( familyStatus === ''
                || familyStatus === null
                || familyStatus === 'null'
                || familyStatus === 'Семейное положение')
                ?
                    null
                :
                    familyStatus;

        let NewPlaceOfResidence = ( (placeOfResidence.current as HTMLInputElement).value === ''
                ||(placeOfResidence.current as HTMLInputElement).value === null
                ||(placeOfResidence.current as HTMLInputElement).value === 'null'
                || (placeOfResidence.current as HTMLInputElement).value === ''
                || (placeOfResidence.current as HTMLInputElement).value === 'Место положения')
                ?
                    null
                :
                    (placeOfResidence.current as HTMLInputElement).value;

        let NewAttitudeToSmocking = (attitudeToSmocking === ''
                || attitudeToSmocking == null
                || attitudeToSmocking == 'null'
                || attitudeToSmocking === 'Отношение к курению'
                || attitudeToSmocking === 'Оставить это поле пустым')
                ?
                    null
                :
                    attitudeToSmocking;

        let NewAttitudeToSport = (attitudeToSport === ''
                || attitudeToSport === null
                || attitudeToSport === 'null'
                || attitudeToSport === 'Отношение к спорту'
                || attitudeToSport === 'Оставить это поле пустым')
                ?
                    null
                :
                    attitudeToSport;

        let NewAttitudeToAlcohol = ( attitudeToAlcohol === ''
                || attitudeToAlcohol === null
                || attitudeToAlcohol === 'null'
                || attitudeToAlcohol === 'Оставить это поле пустым'
                || attitudeToAlcohol === 'Отношение к алкоголю')
                ?
                    null
                :
                    attitudeToAlcohol;

        let NewDreams = ( (dreams.current as HTMLInputElement).value === ''
                ||(dreams.current as HTMLInputElement).value === null
                ||(dreams.current as HTMLInputElement).value === 'null'
                || (dreams.current as HTMLInputElement).value === ''
                || (dreams.current as HTMLInputElement).value === 'Мечта')
                ?
                    null
                :
                    (dreams.current as HTMLInputElement).value;

        //endregion

        function MakeValid(value: string, defaultValue = '') {
            if (value === null || value === 'null' ) {
                SetNotification('Обязательное поле заполнено предыдущим значением.')
                return defaultValue;
            }
            return value;
        }

        // @ts-ignore
        NewName = MakeValid(NewName, 23, true, InfoStore.name);
        // @ts-ignore
        NewSurname = MakeValid(NewSurname, 23, true, InfoStore.surname);
        
        console.log(attitudeToSmocking)
        
        InfoStore.changeInfo({
            friends: InfoStore.friends,
            online: InfoStore.online,
            subscribers: InfoStore.subscribers,
            name: NewName,
            surname: NewSurname,
            birthday: NewBirthday,
            favourites_books: NewFavoriteBooks,
            favourites_films: NewFavoriteFilms,
            favourites_games: NewFavoriteGames,
            favourites_meals: NewFavoriteMeals,
            description: NewDescription,
            family_status: NewFamilyStatus,
            place_of_residence: NewPlaceOfResidence,
            attitude_to_smocking: NewAttitudeToSmocking,
            attitude_to_sport: NewAttitudeToSport,
            attitude_to_alcohol: NewAttitudeToAlcohol,
            dreams: NewDreams,
            avatar: InfoStore.avatar
        })
        SetIsChange(false);
    }, [attitudeToSmocking, attitudeToSport, attitudeToAlcohol, familyStatus]),
    DeclineChanges = useCallback( () => {
        SetIsChange(false);
    }, []);

    const SelectorLists = {
        Alcohol: ['Резко негативное', 'Негативное', 'Компромисное', 'Положительное'],
        Smocking: ['Резко негативное', 'Негативное', 'Компромисное', 'Положительное'],
        Sport: ['Негативное', 'Компромисное', 'Положительно', 'Очень положительное'],
        Family: ['Готов(-а) к отношениям', 'Встречаюсь', 'Есть супруг(-а)', 'Не готов(-а) к отношениям']
    }

    const SetAttitudeToAlcohol = useCallback( (event: SelectChangeEvent<any>, child: any) => {
        setAttitudeToAlcohol(event.target.value.toString());
    }, [])
    const SetAttitudeToSmocking = useCallback( (event: SelectChangeEvent<any>, child: any) => {
        setAttitudeToSmocking(event.target.value.toString());
    }, []);
    const SetAttitudeToSport = useCallback( (event: SelectChangeEvent<any>, child: any) => {
        setAttitudeToSport(event.target.value.toString());
    }, []);
    const SetFamilyStatus = useCallback( (event: SelectChangeEvent<any>, child: any) => {
        setFamilyStatus(event.target.value.toString());
    }, [])

    return (
        <div style={{position: 'relative'}}>
            <div style={{display: 'flex'}}>
                <div>
                    <AlreadyInput ClassName={styles.InfoInput} width={'236px'} Style={NameStyles} label={'Ваше имя'} length={23} reg={{reg: /[^A-Za-zа-яА-Я\d]+/g}} ref={name} design={'default'} value={InfoStore.name !== null ? InfoStore.name : ''}/>
                </div>
                <div>
                    <AlreadyInput ClassName={styles.InfoInput} width={'237px'} Style={SurnameStyles} label={'Ваша фамилия'} length={23} reg={{reg: /[^A-Za-zа-яА-Я\d]+/g}} ref={surname} design={'default'} value={InfoStore.surname !== null ? InfoStore.surname : ''}/>
                </div>
            </div>
            <AlreadyInput width={'467px'} crossStyles={{color: '#333333'}} ClassName={styles.InfoInput} label={'О себе'} length={123} reg={'eng_AND_rus'} ref={description} design={'cross'} value={InfoStore.description !== null ? InfoStore.description : ''}/>
            <InfoDate ref={birthday} placeholder={'День рождения'} value={InfoStore.birthday}/>
            <AlreadyInput width={'467px'} crossStyles={{color: '#333333'}} ClassName={styles.InfoInput} label={'Место положения'} length={123} reg={'eng_AND_rus'} ref={placeOfResidence} design={'cross'} value={InfoStore.place_of_residence !== null ? InfoStore.place_of_residence : ''}/>{/* @ts-ignore*/}
            <InfoSelect SetValue={SetFamilyStatus} value={familyStatus} placeholder={'Семейное положение'} List={SelectorLists.Family}/>{/* @ts-ignore*/}
            <InfoSelect SetValue={SetAttitudeToAlcohol} value={attitudeToAlcohol} placeholder={'Отношение к алкоголю'} List={SelectorLists.Alcohol}/>{/* @ts-ignore*/}
            <InfoSelect SetValue={SetAttitudeToSmocking} value={attitudeToSmocking} placeholder={'Отношение к курению'} List={SelectorLists.Smocking}/>{/* @ts-ignore*/}
            <InfoSelect SetValue={SetAttitudeToSport} value={attitudeToSport} placeholder={'Отношение к спорту'} List={SelectorLists.Sport}/>
            <AlreadyInput width={'467px'} crossStyles={{color: '#333333'}} ClassName={styles.InfoInput} label={'Любимая еда'} length={123} reg={'all'} ref={favoriteMeals} design={'cross'} value={InfoStore.favourites_meals !== null ? InfoStore.favourites_meals : ''}/>
            <AlreadyInput width={'467px'} crossStyles={{color: '#333333'}} ClassName={styles.InfoInput} label={'Любимые фильмы и сериалы'} length={123} reg={'all'} ref={favoriteFilms} design={'cross'} value={InfoStore.favourites_films !== null ? InfoStore.favourites_films : ''}/>
            <AlreadyInput width={'467px'} crossStyles={{color: '#333333'}} ClassName={styles.InfoInput} label={'Любимые книги'} length={123} reg={'all'} ref={favoriteBooks} design={'cross'} value={InfoStore.favourites_books !== null ? InfoStore.favourites_books : ''}/>
            <AlreadyInput width={'467px'} crossStyles={{color: '#333333'}} ClassName={styles.InfoInput} label={'Любимые игры'} length={123} reg={'all'} ref={favoriteGames} design={'cross'} value={InfoStore.favourites_games !== null ? InfoStore.favourites_games : ''}/>
            <AlreadyInput width={'467px'} crossStyles={{color: '#333333'}} ClassName={styles.InfoInput} label={'Мечта'} length={123} reg={'all'} ref={dreams} design={'cross'} value={InfoStore.dreams !== null ? InfoStore.dreams : ''}/>
            <div className={styles.ButtonInChangeMenu} onClick={DeclineChanges} style={{marginRight: '6px', width: '210px'}}>
                <div className={styles.Text}>Отклонить изменения</div>
            </div>
            <div className={styles.ButtonInChangeMenu} onClick={AcceptChanges} style={{width: '185px'}}>
                <div className={styles.Text}>Принять изменения</div>
            </div>
        </div>
    );

});