import {hand, RestApiMethods} from "../../../../helper/hand";
import {eye} from "../../../config";
import {runInAction} from "mobx";
import {PostState} from "../../../PostsState";
import {Post} from "../../../../types/PostTypes";

export const UnLikePostFetch = (postId: number) => {
    return hand(`/api/post/${postId}/likes/unlike`, RestApiMethods.Patch)
        .then (response => {
            if (response?.status !== 204) {
                eye.warn(`Failed to unlike post ${postId}!`);
            } else {
                runInAction(() => {
                    (PostState.Posts.find(post => post.id === postId) as Post).likes -= 1;
                })
            }
        })
}