import {action, observable, runInAction} from "mobx";
import {Comment, CommentDTO} from "../types/PostTypes";
import {DOMAIN} from "../helper/api";
import {SetNotification} from "../helper/SetNotification";
import {USERID} from "../USERID";
import {hand, RestApiMethods} from "../helper/hand";
import {UsersStore} from "./UsersStore";

interface CommentStoreInterface {
    /** The number of the post that has this comments*/
    id: number;
    comments: Comment[];
    loading: boolean;

    readonly add: (postId: number, comment: CommentDTO) => void;
    readonly delete: (numberOfComment: number) => void;
    readonly like: (numberOfComment: number) => Promise<void>;
    readonly dislike: (numberOfComment: number) => Promise<void>;
    readonly unlike:  (numberOfComment: number) => Promise<void>;
    readonly undislike: (numberOfComment: number) => Promise<void>;
    /** This method gets comments from server and change the `comments` attribute.*/
    readonly getComments: (numberOfPost: number, offset: number) => void;
}

export const CommentStore = observable<CommentStoreInterface>({
    id: -1, comments: [], loading: false,

    add: action((postId: number, comment: CommentDTO) => {
        hand('/api/comment/' + postId, RestApiMethods.Post, JSON.stringify({
            parent_comment_id: comment.ParentCommentId,
            data: comment.Data,
            files: comment.Files
        }))
        .then (response => {
            if (response?.status === 201) {
                return response?.json();
            } else {
                throw new Error(response?.statusText);
            }
        }).then ((Response: {commentId: number}) => {
            runInAction( () => {
                CommentStore.comments.unshift({
                    data: comment.Data,
                    date: "Только что",
                    disliked_by: [+USERID],
                    parent_comment_id: comment.ParentCommentId,
                    dislikes: 0,
                    files: comment.Files,
                    liked_by: [+USERID],
                    likes: 0,
                    id: Response.commentId,
                    parent_post_id: postId,
                    parent_user_id: +USERID,
                });
            })
        })
    }),

    delete: action((commentId: number) => {
        hand('/api/comment/' + commentId, RestApiMethods.Delete)
        .then (response => {
            if (response?.status === 204) {
                let foundIndex = -1;
                for (let i = 0; i < CommentStore.comments.length; i++) {
                    if (CommentStore.comments[i].id === commentId) {
                        foundIndex = i;
                        break;
                    }
                }
                if (foundIndex >= 0) {
                    CommentStore.comments.splice(foundIndex, 1);
                }
            }
        })
    }),

    getComments: action((postId: number, offset: number) => {
        CommentStore.loading = true;
        CommentStore.id = postId;
        hand('/api/comment/' + postId + "?offset=" + offset, RestApiMethods.Get)
        .then (response => {
            if (response?.status === 200) {
                return response?.json();
            }
        }).then (async (Response: {comments: Comment[]}) => {
            runInAction( () => {
                if (offset < 1) {
                    CommentStore.comments = Response.comments;
                } else {
                    CommentStore.comments.push(...Response.comments)
                }
            })
            const needToGet: number[] = [];
            Response.comments.forEach(comment => {
                if (UsersStore.users.find(user => user.ID === comment.parent_user_id) == undefined) {
                    needToGet.push(comment.parent_user_id);
                }
            })
            if (needToGet.length > 0) {
                await UsersStore.getUsers(needToGet)
            }
            runInAction(() => {
                CommentStore.loading = false;
            })
        })
    }),

    like: action((numberOfComment: number) => {
        return hand('/api/comment/' + numberOfComment + '/likes/like', RestApiMethods.Patch)
        .then (response => {
            if (response?.status === 204) {
                const foundIndex = CommentStore.comments.findIndex(comment => comment.id === numberOfComment);
                if (foundIndex >= 0) {
                    CommentStore.comments[foundIndex].likes += 1;
                    CommentStore.comments[foundIndex].liked_by.push(+USERID)
                }
            }
        })
    }),

    dislike: action((numberOfComment: number) => {
        return hand('/api/comment/' + numberOfComment + '/dislikes/dislike', RestApiMethods.Patch)
            .then (response => {
                if (response?.status === 204) {
                    const foundIndex = CommentStore.comments.findIndex(comment => comment.id === numberOfComment);
                    if (foundIndex >= 0) {
                        CommentStore.comments[foundIndex].dislikes += 1;
                        CommentStore.comments[foundIndex].disliked_by.push(+USERID)
                    }
                }
            })
    }),

    unlike: action((numberOfComment: number) => {
        return hand('/api/comment/' + numberOfComment + '/likes/unlike', RestApiMethods.Patch)
            .then (response => {
                if (response?.status === 204) {
                    const foundIndex = CommentStore.comments.findIndex(comment => comment.id === numberOfComment);
                    if (foundIndex >= 0) {
                        CommentStore.comments[foundIndex].likes -= 1;
                        CommentStore.comments[foundIndex].liked_by.splice(CommentStore.comments[foundIndex].liked_by.indexOf(+USERID), 1)
                    }
                }
            })
    }),

    undislike: action((numberOfComment: number) => {
        return hand('/api/comment/' + numberOfComment + '/dislikes/undislike', RestApiMethods.Patch)
            .then (response => {
                if (response?.status === 204) {
                    const foundIndex = CommentStore.comments.findIndex(comment => comment.id === numberOfComment);
                    if (foundIndex >= 0) {
                        CommentStore.comments[foundIndex].dislikes -= 1;
                        CommentStore.comments[foundIndex].disliked_by.splice(CommentStore.comments[foundIndex].disliked_by.indexOf(+USERID), 1)
                    }
                }
            })
    })
});