import React, {memo, FC} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {DOMAIN} from "../../../helper/api";
import {VideoStore} from "../../../LayOut/store/stores/VideoStore";
import {USERID} from "../../../USERID";

interface Props {
    Videos: string[]
}

export const FileBlockVideo: FC<Props> = memo (({Videos}) => {

    function PlayVideo(Index:number) {
        VideoStore.openNewVideo(Videos[Index], DOMAIN + '/UserFiles/' + USERID + '/Video/' + Videos[Index]);
    }

    return (
        <div className={styles.VideoBlockInCreate}>
            {
                Videos.map( (Video, Index) => {
                    return (
                        <div key={Index} style={{height: '90px', marginBottom: '5px'}} onClick={ () => {PlayVideo(Index)} }>
                            <svg style={{position: 'relative', top: '-30px', left: '65px', zIndex: '2'}} height={'30'} viewBox="0 0 100 100">
                                <g>
                                    <path
                                        d="M31.356,25.677l38.625,22.3c1.557,0.899,1.557,3.147,0,4.046l-38.625,22.3c-1.557,0.899-3.504-0.225-3.504-2.023V27.7   C27.852,25.902,29.798,24.778,31.356,25.677z"/>
                                    <path
                                        d="M69.981,47.977l-38.625-22.3c-0.233-0.134-0.474-0.21-0.716-0.259l37.341,21.559c1.557,0.899,1.557,3.147,0,4.046   l-38.625,22.3c-0.349,0.201-0.716,0.288-1.078,0.301c0.656,0.938,1.961,1.343,3.078,0.699l38.625-22.3   C71.538,51.124,71.538,48.876,69.981,47.977z"/>
                                    <path
                                        d="M31.356,25.677l38.625,22.3c1.557,0.899,1.557,3.147,0,4.046   l-38.625,22.3c-1.557,0.899-3.504-0.225-3.504-2.023V27.7C27.852,25.902,29.798,24.778,31.356,25.677z"
                                        style={{fill: '#ffffff', stroke: '#ffffff', strokeMiterlimit: '10'}}/>
                                </g>
                            </svg>
                            <video className={styles.videoIcon} src={DOMAIN + '/UserFiles/' + USERID + '/Video/' + Video}/>
                            <div className={styles.text}>
                                {Video}
                            </div>
                        </div>
                    )
                })
            }
        </div>
    );

});