import React, {FC, useCallback, useRef} from 'react';
import styles from "../../../styles/MainPart.module.scss";
import {SetLoading} from "../../../helper/SetLoading";
import {observer} from "mobx-react-lite";
import {CommentStore} from "../../../store/CommentStore";
import {AlreadyInput} from "../../../helper/AlreadyInput";
import {CommentDTO} from "../../../types/PostTypes";
import {Comment} from "../../UI/Post/Comment";

export const CommentBlock: FC<{ id: number, setShow: (value: boolean) => void }> = observer(({id, setShow}) => {

    const input = useRef<HTMLInputElement>(null);

    const enter = useCallback(() => {
        if ((input.current as HTMLInputElement).value == '') return;
        const comment:CommentDTO = (() => {
            return {
                ParentCommentId: 0,
                Files: [],
                Data: (input.current as HTMLInputElement).value,
            };
        })();
        CommentStore.add(id, comment)
    }, []);

    if (CommentStore.id === id) {
        if (CommentStore.loading) {
            return (
                <div className={styles.CommentBlock}>
                    <SetLoading styles={{position: 'relative', top: '85px'}}/>
                </div>
            );
        } else {
            return (
                <div className={styles.CommentBlock}>
                    <div className={styles.inputLine}>
                        <AlreadyInput onEnter={enter} ClassName={styles.input} length={255} reg={'all'} ref={input} design={'arrowDown'} placeholder={'Оставьте ваш комментарий здесь'}/>
                    </div>
                    {CommentStore.comments.length === 0
                        ?
                            <div className={styles.noCommentsText}>
                                Эту запись ещё никто не прокоментировал. Будьте первыми!
                            </div>
                        :
                            <>
                                {
                                    CommentStore.comments.map( (value) => {
                                        return (
                                            <Comment
                                                key={value.id} date={value.date}
                                                parent_comment_id={value.parent_comment_id}
                                                files={value.files}
                                                id={value.id}
                                                parent_post_id={value.parent_post_id}
                                                parent_user_id={value.parent_user_id}
                                                data={value.data} likes={value.likes} dislikes={value.dislikes}
                                                liked_by={value.liked_by} disliked_by={value.disliked_by}
                                            />
                                        )
                                    })
                                }
                            </>
                    }
                </div>
            );
        }
    } else {
        setShow(false);
        return <></>;
    }
});