import React, {memo, FC} from 'react';
import styles from '../../../styles/MainPart.module.scss';

interface Props {
    Content: string | null;
    Styles?: React.CSSProperties;
    id: string;
}

export const TextBlock: FC<Props> = memo (({Content, Styles, id}) => {

    return (
        <>
            {Content === null
                ?
                <></>
                :
                    <div style={Styles} id={id} className={styles.TextBlock}>
                        {Content}
                    </div>
            }
        </>
    );
});