import {Eye} from "./helper/eye";
import {eye} from "./store/config";
import {InfoStore} from "./store/InfoStore";
import {PageId} from "./helper/GetPage";

enum connectionResponseMethods {
    getOnlineUsers = "getOnlineUsers",
    userDisconnected = "userDisconnected",
    userConnected = "userConnected",
    newMessage = "newMessage",
    updateMessage = "updateMessage",
    deleteMessage = "deleteMessage",
    newChat = "newChat",
    updateChat = "updateChat",
    deleteChat = "deleteChat"
}

enum connectionRequestMethods {
    getOnlineUsers = "getOnlineUsers",
    sendMessage = "sendMessage",
    updateMessage = "updateMessage",
    deleteMessage = "deleteMessage",
    createChat = "createChat",
    updateChat = "updateChat",
    deleteChat = "deleteChat"
}

export class DefaultConnection {
    // @ts-ignore
    public WebConnection: WebSocket;
    private readonly handler: (response: {method: connectionResponseMethods, data: any}) => void;
    private readonly eye: Eye;
    public readonly url: string;
    private readonly waitingFor: [connectionResponseMethods, string][];
    constructor(url: string, eye: Eye, handler: (response: {method: connectionResponseMethods, data: any}) => void) {
        this.eye = eye;
        this.url = url;
        this.handler = handler;
        this.waitingFor = [];
    }

    start(this: DefaultConnection) {
        this.eye.newLogGroup("Connecting", {name: "Configuring connection"}, 'Connection was opened')
        this.WebConnection = new WebSocket(this.url);
        this.eye.stepReady("Connecting", "Configuring connection")
        this.eye.newStep("Connecting", {name: "Opening connection"})
        this.WebConnection.onmessage = (e: MessageEvent) => {
            if (e.data == "Welcome") {
                eye.stepReady("Connecting", "Opening connection", true)
                return;
            }
            const message: any = JSON.parse(e.data);
            this.handleResponse(message);
        }
    }

    handleResponse(this: DefaultConnection, response: {method: connectionResponseMethods, data: any}) {
        this.eye.info(`Got response from ${this.WebConnection.url}: ${response.method}`)
        const index = this.waitingFor.findIndex(e => e[0] == response.method)
        if (index > -1) {
            this.eye.stepReady(this.waitingFor[index][1], `Response handler to method ${this.waitingFor[index][0]}`)
            this.eye.newStep(this.waitingFor[index][1], {name: `Handling to method ${this.waitingFor[index][0]}`})
        }
        this.handler(response)
        if (index > - 1) {
            this.eye.stepReady(this.waitingFor[index][1], `Handling to method ${this.waitingFor[index][0]}`, true)
            this.waitingFor.splice(index, 1)
        }
    }

    send(this: DefaultConnection, method: connectionRequestMethods, data: any, responseMethod: connectionResponseMethods | null = null, groupName: string | null = null) {
        if (responseMethod != null && groupName != null) {
            this.waitingFor.push([responseMethod, groupName]);
            this.eye.newLogGroup(groupName, {name: method}, groupName)
            this.WebConnection.send(JSON.stringify({method: method, data: data}))
            this.eye.newStep(groupName, {name: `Response handler to method ${method}`})
            this.eye.stepReady(groupName, method)
        } else {
            this.eye.info(`Sending ${method} to ${this.WebConnection.url}`)
            this.WebConnection.send(JSON.stringify({method: method, data: data}))
        }
    }

    saveSend(this: DefaultConnection, method: connectionRequestMethods, data: any, responseMethod: connectionResponseMethods | null = null, groupName: string | null = null) {
        if (this.WebConnection.bufferedAmount == 0) {
            this.send(method, data, responseMethod, groupName)
        }
    }
}

export function handler(response: {method: connectionResponseMethods, data: any}) {
    switch (response.method) {
        case connectionResponseMethods.getOnlineUsers:
            if (response.data.indexOf(+PageId) != -1) {
                InfoStore.getOnlineStatus(true);
            } else {
                InfoStore.getOnlineStatus(false);
            }
            break;

        case connectionResponseMethods.userConnected: {
            InfoStore.reactToOnline(+response.data)
            break;
        }

        case connectionResponseMethods.userDisconnected: {
            InfoStore.reactToOffline(+response.data)
            break;
        }

        case connectionResponseMethods.newChat: {
            break;
        }

        case connectionResponseMethods.updateChat: {
            break
        }

        case connectionResponseMethods.deleteChat: {
            break;
        }

        case connectionResponseMethods.newMessage: {
            break;
        }

        case connectionResponseMethods.deleteMessage: {
            break;
        }

        case connectionResponseMethods.updateMessage: {
            break;
        }
    }
}

export class WebSocketConnection extends DefaultConnection {
    checkUsersOnOnline(this: WebSocketConnection, users: number[]) {
        this.saveSend(connectionRequestMethods.getOnlineUsers, users, connectionResponseMethods.getOnlineUsers, 'Getting online users')
    }

    createChat(this: WebSocketConnection, name: string, avatar: string | "", members: number[]) {
        const obj = {
            name: name,
            avatar: avatar,
            members: members
        }
        this.saveSend(connectionRequestMethods.createChat, obj, connectionResponseMethods.newChat, 'Creating new chat')
    }

    updateChat(this: WebSocketConnection, name: string, avatar: string | "", members: number[], chatId: number) {
        const obj = {
            name: name,
            avatar: avatar,
            members: members,
            chat_id: chatId
        }
        this.saveSend(connectionRequestMethods.updateChat, obj, connectionResponseMethods.updateChat, 'Updating a chat')
    }

    deleteChat(this: WebSocketConnection, chatId: number) {
        this.saveSend(connectionRequestMethods.deleteChat, chatId, connectionResponseMethods.deleteChat, 'Deleting a chat')
    }

    sendMessage(this: WebSocketConnection, data: {ParentChatID: number, ParentUserID: number, Data: string, Files: string[], MessageParentID: number | 0}) {
        let obj = {
            parent_chat_id: data.ParentChatID,
            parent_user_id: data.ParentUserID,
            data: data.Data,
            files: data.Files,
            message_parent_id: data.MessageParentID
        }
        this.saveSend(connectionRequestMethods.sendMessage, obj, connectionResponseMethods.newMessage, 'Sending a message')
    }

    updateMessage(this: WebSocketConnection, messageID: number, newData: string) {
        let obj = {
            message_id: messageID,
            data: newData
        }
        this.saveSend(connectionRequestMethods.updateMessage, obj, connectionResponseMethods.updateMessage, 'Updating a message')
    }

    deleteMessage(this: WebSocketConnection, messageID: number) {
        this.saveSend(connectionRequestMethods.deleteMessage, messageID, connectionResponseMethods.deleteMessage, 'Deleting a message')
    }
}

export const Connection = new WebSocketConnection(`ws://localhost:4040/ws`, eye, handler);