import {hand, RestApiMethods} from "../../../../helper/hand";
import {eye} from "../../../config";
import {runInAction} from "mobx";
import {PostState} from "../../../PostsState";

export const DeletePostFetch = (postId: number) => {
    return hand('/api/post/' + postId, RestApiMethods.Delete)
        .then(response => {
            if (response?.status != 204) {
                eye.warn(`Failed to delete post ${postId}!`);
            } else {
                runInAction(() => {
                    PostState.Posts.splice(PostState.Posts.findIndex(post => post.id == postId), 1);
                })
            }
        })
}
