import {action, observable, runInAction} from "mobx";
import {DOMAIN} from "../helper/api";
import {SetNotification} from "../helper/SetNotification";
import {PageId} from "../helper/GetPage";
import {USERID} from "../USERID";
import {FriendStatusStore} from "./FriendStatusStore";
import {Connection} from "../Connection";
import {hand, RestApiMethods} from "../helper/hand";
import {eye} from "./config";

export type info = {
    name: string | null;
    surname: string | null;
    avatar: string | null;
    birthday: string | null;
    favourites_books: string | null;
    favourites_films: string | null;
    favourites_games: string | null;
    favourites_meals: string | null;
    description: string | null;
    family_status: string | null;
    place_of_residence: string | null;
    attitude_to_smocking: string | null;
    attitude_to_sport: string | null;
    attitude_to_alcohol: string | null;
    dreams: string | null;
    friends: number[];
    subscribers: number[];
    online: boolean;
}

interface InfoStoreInterface extends info{
    readonly getInfo: () => void;
    readonly changeInfo: (payload: info) => void;
    readonly changeAvatar: (File: File) => void;
    readonly reactToOnline: (id: number) => void;
    readonly reactToOffline: (id: number) => void;
    readonly getOnlineStatus: (value: boolean) => void;
}

export const InfoStore = observable<InfoStoreInterface>({
    online: false,
    name: null,
    surname: null,
    avatar: null,
    birthday: null,
    favourites_books: null,
    favourites_films: null,
    favourites_games: null,
    favourites_meals: null,
    description: null,
    family_status: null,
    place_of_residence: null,
    attitude_to_smocking: null,
    attitude_to_sport: null,
    attitude_to_alcohol: null,
    dreams: null,
    subscribers: [],
    friends: [],

    getInfo: action(() => {
        hand('/api/user/' + ( PageId ) + ((+USERID > 0 && typeof +USERID === "number" && !isNaN(+USERID)) ? "?requestOwner=" + (+USERID) : ''), RestApiMethods.Get)
            .then(res => res?.json())
            .then (Response => {
                runInAction( () => {
                    FriendStatusStore.urOwnerSubs = Response.subs
                    InfoStore.name = Response.user.name;
                    InfoStore.surname = Response.user.surname;
                    InfoStore.birthday = Response.user.birthday;
                    InfoStore.family_status = Response.user.family_status;
                    InfoStore.favourites_books = Response.user.favourites_books;
                    InfoStore.favourites_films = Response.user.favourites_films;
                    InfoStore.favourites_games = Response.user.favourites_games;
                    InfoStore.favourites_meals = Response.user.favourites_meals;
                    InfoStore.description = Response.user.description;
                    InfoStore.avatar = Response.user.avatar;
                    InfoStore.place_of_residence = Response.user.place_of_residence;
                    InfoStore.attitude_to_smocking = Response.user.attitude_to_smocking;
                    InfoStore.attitude_to_sport = Response.user.attitude_to_sport;
                    InfoStore.attitude_to_alcohol = Response.user.attitude_to_smocking;
                    InfoStore.dreams = Response.user.dreams;
                    InfoStore.friends = Response.user.friends;
                    InfoStore.subscribers = Response.user.subscribers;
                })
                FriendStatusStore.getIsFriendsState();
                Connection.start();
            })
    }),

    changeInfo: action((info: info) => {
        hand('/api/user/', RestApiMethods.Patch, JSON.stringify({
            name: info.name,
            surname: info.surname,
            avatar: info.avatar,
            birthday: info.birthday,
            favourites_books: info.favourites_books,
            favourites_films: info.favourites_films,
            favourites_games: info.favourites_games,
            favourites_meals: info.favourites_meals,
            description: info.description,
            family_status: info.family_status,
            place_of_residence: info.place_of_residence,
            attitude_to_smocking: info.attitude_to_smocking,
            attitude_to_sport: info.attitude_to_sport,
            attitude_to_alcohol: info.attitude_to_alcohol,
            dreams: info.dreams,
        })).then(response => {
                if (response?.status === 204) {
                    runInAction( () => {
                        InfoStore.name = info.name;
                        InfoStore.surname = info.surname;
                        InfoStore.avatar = info.avatar;
                        InfoStore.birthday = info.birthday;
                        InfoStore.favourites_books = info.favourites_books;
                        InfoStore.favourites_films = info.favourites_films;
                        InfoStore.favourites_games = info.favourites_books;
                        InfoStore.favourites_meals = info.favourites_meals;
                        InfoStore.description = info.description;
                        InfoStore.family_status = info.family_status;
                        InfoStore.place_of_residence = info.place_of_residence;
                        InfoStore.attitude_to_smocking = info.attitude_to_smocking;
                        InfoStore.attitude_to_sport = info.attitude_to_sport;
                        InfoStore.attitude_to_alcohol = info.attitude_to_smocking;
                        InfoStore.dreams = info.dreams;
                    });
                }
            })
    }),

    changeAvatar: action((File: File) => {
        const FormDataUpload = new FormData();
        FormDataUpload.append('avatar', File)
        const time = Date.now();
        eye.fetchSend(DOMAIN + '/api/user/avatar/', RestApiMethods.Post)
        fetch(DOMAIN + '/api/user/avatar/', {
            method: 'POST',
            credentials: 'include',
            body: FormDataUpload
        }).then(async (Res) => {
                eye.fetchGet(DOMAIN + '/api/user/avatar/', RestApiMethods.Post, Res.status, Date.now() - time)
                if (Res?.status !== 200) {
                    SetNotification('Неожиданная ошибка!')
                } else {
                    runInAction(() => {
                        InfoStore.avatar = File.name;
                    })
                }
            })
            .catch(() => {
                eye.fetchGet(DOMAIN + '/api/user/avatar/', RestApiMethods.Post, 500, Date.now() - time)
            })
    }),

    reactToOnline: action((id: number) => {
        if (id === +PageId) {
            InfoStore.online = true;
        }
    }),

    reactToOffline: action((id: number) => {
        if (id === +PageId) {
            InfoStore.online = false;
        }
    }),

    getOnlineStatus: action((value: boolean) => {
        InfoStore.online = value;
    })
})