import React, {memo, FC, useRef} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {Checkbox} from "@mui/material";

export const SurveyInSiteLine: FC<{ text: string, isChosen: boolean, isItChosen: boolean, index:number,
    setChosen: (index: number) => void, IsMultipleChoice: boolean, numberOfVoice: number, isFirstChosen: boolean }> =
    memo (({text, setChosen, isChosen, IsMultipleChoice, isItChosen, numberOfVoice, index, isFirstChosen}) => {

    return (
        <div style={isItChosen ? {cursor: 'default', backgroundColor: 'rgba(255, 255, 255, .5)'} : isChosen ? {cursor: 'default'} : {}}
             className={styles.surveyLine} onClick={ () => {setChosen(index);}}>
            <div className={IsMultipleChoice ? styles.textBlock + ' ' + styles.multi : styles.textBlock}>
                <div className={styles.text}>
                    {text}
                </div>
                {isChosen
                    ?
                        <div className={styles.number}>{!isFirstChosen && isItChosen ? numberOfVoice + 1 :  numberOfVoice}</div>
                    :
                        <></>
                }
            </div>
            {IsMultipleChoice
                ?
                    !isChosen
                    ?
                        <Checkbox onChange={() => {setChosen(index)} } checked={isItChosen}/>
                    :
                        isItChosen
                            ?
                                <Checkbox onChange={() => {setChosen(index)} } checked={isItChosen}/>
                            :
                                <></>
                :
                    <></>
            }
        </div>
    );

});