import {hand, RestApiMethods} from "./helper/hand";

if (PageId === null || isNaN(PageId) || PageId < 1) {
    hand("/api/user", RestApiMethods.Patch, JSON.stringify({}))
   alert("Неверный адрес!")//TODO послать пользователя на страницу для неверных адресов
} else {
    ReactDOM.createRoot( (document.getElementById ("root") as HTMLElement) ).render (
        <MainLayOut>
            <MainPart/>
        </MainLayOut>
    );
}
