import React, {memo, FC, useState, useRef, useEffect, useCallback} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import ThumbDownIcon from '@mui/icons-material/ThumbDown';
import {UnLikePostFetch} from "../../../store/fetch/Posts/LikeOrDislike/UnLikePost";
import {LikePostFetch} from "../../../store/fetch/Posts/LikeOrDislike/LikePost";
import {DislikePostFetch} from "../../../store/fetch/Posts/LikeOrDislike/DislikePost";
import {UnDislikePostFetch} from "../../../store/fetch/Posts/LikeOrDislike/UnDislikePost";
import DeleteIcon from '@mui/icons-material/Delete';
import {DeletePostFetch} from "../../../store/fetch/Posts/AddOrDelete/DeletePost";
import CommentIcon from '@mui/icons-material/Comment';
import {CommentBlock} from "./CommentBlock";
import {CommentStore} from "../../../store/CommentStore";
import {PageId} from "../../../helper/GetPage";
import {USERID} from "../../../USERID";

export const PostBottom: FC<{likes: number, likedBy: number[], id: number,
        dislikes: number, dislikedBy: number[]
}> = memo (({dislikes, likes, likedBy, dislikedBy, id}) => {

    const estimationOnStart = useRef<'liked' | 'disliked' | 'none' |'author'> (PageId == +USERID ? 'author' :// @ts-ignore
        likedBy.indexOf( +USERID ) !== -1 ? 'liked' : dislikedBy.indexOf( +USERID ) !== -1 ? 'disliked' : 'none');
    const [estimation, setEstimation] = useState<'liked' | 'disliked' | 'none' | 'author'> (estimationOnStart.current)
    const [isCommentShowing, setIsCommentShowing] = useState (false);

    const like = useRef<HTMLDivElement>(null),
      dislike = useRef<HTMLDivElement>(null);
    
    const likeThis = useCallback (() => {
        if (estimationOnStart.current === 'author') return;
        if (estimation !== 'liked') {
            if (estimation === 'disliked' || dislikedBy.indexOf( +USERID ) !== -1) {
                UnDislikePostFetch(id)
                    .then( () => {LikePostFetch(id);} );
            } else {
                LikePostFetch(id);
            }
            setEstimation('liked');
        } else {
            UnLikePostFetch(id);
            setEstimation('none');
        }
    }, [estimation]);

    const dislikeThis = useCallback( () => {
        if (estimationOnStart.current === 'author') return;
        if (estimation !== 'disliked') {
            if (estimation === 'liked' || likedBy.indexOf(+USERID ) !== -1) {
                UnLikePostFetch(id)
                    .then( () => {DislikePostFetch(id);} );
            } else {
                DislikePostFetch(id)
            }
            setEstimation('disliked');
        } else {
            UnDislikePostFetch(id);
            setEstimation('none');
        }
    }, [estimation])

    const SetIsCommentShowing = useCallback((value: boolean) => {
        setIsCommentShowing(value);
    }, []);

    useEffect (() => {
        switch (estimation) {
            case "liked":
                (dislike.current as HTMLDivElement).style.backgroundColor = '';
                (like.current as HTMLDivElement).style.backgroundColor = 'rgba(255, 255, 255, 0.3)';
                break;
            case "disliked":
                (like.current as HTMLDivElement).style.backgroundColor = '';
                (dislike.current as HTMLDivElement).style.backgroundColor = 'rgba(255, 255, 255, 0.3)';
                break;

            default:
                (like.current as HTMLDivElement).style.backgroundColor = '';
                (dislike.current as HTMLDivElement).style.backgroundColor = '';
                return;
        }
    }, [estimation]);

    return (
        <div className={styles.PostBottom}>
            <div className={styles.iconPanel}>
                <div style={{display: 'flex'}}>
                    <div className={styles.likesAndDislikesBlock}>
                        <div ref={like} onClick={likeThis} className={styles.estimationBlock + ' ' + styles.likes}>
                            <ThumbUpIcon style={{fill: 'white'}} />
                            {likes}
                        </div>
                        <div ref={dislike} onClick={dislikeThis} className={styles.estimationBlock + ' ' + styles.dislikes}>
                            <ThumbDownIcon style={{fill: 'white'}} />
                            {dislikes}
                        </div>
                    </div>
                    <div className={styles.commentIcon}
                         onClick={() => {
                             setIsCommentShowing(!isCommentShowing);// todo offset
                             if (CommentStore.id !== id) CommentStore.getComments(id, 0);
                         }}
                    >
                        <CommentIcon className={styles.icon}/>
                    </div>
                </div>
                {PageId === +USERID
                    ?
                        <DeleteIcon className={styles.trash} onClick={
                            () => {
                                DeletePostFetch(id);
                            }
                        }/>
                    :
                        <></>
                }
            </div>
            {isCommentShowing
                ?
                    <CommentBlock id={id} setShow={SetIsCommentShowing}/>
                :
                    <></>
            }

        </div>
    );

});