import React, {memo, FC} from 'react';

export const BackgroundColorBlock: FC<{onClick: any, backgroundColor: string}> = memo (({onClick, backgroundColor}) => {

    return (
        <div onClick={() => {onClick(backgroundColor)} } style={{border: 'solid 1px #fff', width: '55px', cursor: 'pointer',
            height: '55px', background: backgroundColor, display: 'inline-block', marginRight: '5px'}}/>
    );

});