import React, {memo, FC, useState, useRef, useEffect, useCallback} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import GlobalStyles from '../../../LayOut/styles/GlobalStyles.module.scss';
import {FileBlock} from "./FileBlock";
import {CreatePostButtonsPanel} from "./CreatePostButtonsPanel";
import {SetNotification} from "../../../helper/SetNotification";
import {SurveyBLock} from "./SurveyBLock";
//import {UploadFile} from "../../../helper/UploadFile";
import {CheckText} from "../../../helper/AlreadyInput";
import {PostState} from "../../../store/PostsState";
import {USERID} from "../../../USERID";
import {CreateAPostDTO, CreateASurveyDTO} from "../../../types/PostTypes";

let PostText = '';
// @ts-ignore
export const surveyLines: string[] = ["", ""] ;
export const showingTo: {current: 'All' | 'Friends'} = {current: 'All'};
export const Files: File[]= []

export const CreatePostElement: FC = memo (() => {

    const [IsCreating, SetIsCreating] = useState(false),
        [AudioFiles, SetAudioFiles] = useState<string[]>([]),
        [VideoFiles, SetVideoFiles] = useState<string[]>([]),
        [ImageFiles, SetImageFiles] = useState<string[]>([]),
        [OtherFiles, SetOtherFiles] = useState<string[]>([]);
    const [backgroundColor, setBackgroundColor] = useState ('#C7D2DA');

    const [surveySwitch, setSurveySwitch] = useState (false);
    const [isSurveyCreating, setIsSurveyCreating] = useState (false);//@ts-ignore
    const Text = useRef<HTMLDivElement>(null);
    const numberOfFiles = useRef(0);
    const setNumberOfFiles = useCallback (() => {
        numberOfFiles.current = numberOfFiles.current + 1;
    }, []);
    const SetAudioFiles1 = useCallback ((value: string[]) => {
        SetAudioFiles(value);
    }, []);
    const SetVideoFiles1 = useCallback ((value: string[]) => {
        SetVideoFiles(value);
    }, []);
    const SetImageFiles1 = useCallback ((value: string[]) => {
        SetImageFiles(value);
    }, []);
    const SetOtherFiles1 = useCallback ((value: string[]) => {
        SetOtherFiles(value);
    }, []);
    const TextFilter = useCallback (() => {
        const checkedText = CheckText((Text.current as HTMLDivElement).innerText, '', 4095, 'all', false);
        if (checkedText !== (Text.current as HTMLDivElement).innerText) {
            (Text.current as HTMLDivElement).innerText = checkedText;
        }
    }, []);
    const TextFilterAfterBlur = useCallback (() => {
        const checkedText = CheckText((Text.current as HTMLDivElement).innerText, '', 4095, 'all', true);
        if (checkedText !== (Text.current as HTMLDivElement).innerText) {
            (Text.current as HTMLDivElement).innerText = checkedText;
        }
    }, []);
    const setIsSurveyCreating1 = useCallback (() => {
        setIsSurveyCreating(!isSurveyCreating);
    }, [isSurveyCreating]);
    const SetSurveySwitch = useCallback ((value: boolean) => {
        setSurveySwitch(value);
    }, [surveySwitch]);

    const SetBackgroundColor = useCallback ((color: string) => {
        setBackgroundColor(color);
    }, []);

    const createPost = () => {
        /**It`s an array that has all filled in survey lines to remove empty lines next*/
        let FilledSurveyLines: string[] = [];
        for (let i = 0;i < surveyLines.length; ++i) {
            if (surveyLines[i] !== '') {
                FilledSurveyLines.push(surveyLines[i]);
            }
        }

        if (FilledSurveyLines.length < 2) {
            if (FilledSurveyLines.length === 1) {
                SetNotification('Для опроса допустимо ноль или более одного варианта ответа. Добавьте второй или удалите содержимое единственного варианта ответа!');
                return;
            }
            FilledSurveyLines = [];
        }

        if (ImageFiles.length === 0 && VideoFiles.length === 0 && AudioFiles.length === 0 && OtherFiles.length === 0
            && FilledSurveyLines.length < 2 && (Text.current as HTMLDivElement).innerText === '') {
            return;
        }

        const Post: CreateAPostDTO = {
            Data: (Text.current as HTMLDivElement).innerText,
            Files: ImageFiles.concat(VideoFiles).concat(AudioFiles).concat(OtherFiles),
            HaveASurvey: FilledSurveyLines.length > 1,
        }, Survey: CreateASurveyDTO | "" = FilledSurveyLines.length > 1 ? {
            Background: backgroundColor === '#C7D2DA' ? '#C7D2DA' : backgroundColor,
            Data: FilledSurveyLines,
            is_multi_voices: surveySwitch
        } : "";
        PostState.addPost(Post, Survey, Files)
        SetAudioFiles([]);
        SetOtherFiles([]);
        Files.splice(0, Files.length);
        PostText = '';
        SetVideoFiles([]);
        SetImageFiles([]);
        (Text.current as HTMLDivElement).innerText = '';
        surveyLines.splice(0, surveyLines.length);
        surveyLines[0] = '';
        surveyLines[1] = '';
        setSurveySwitch(false);
        showingTo.current = 'All';
        setBackgroundColor('#C7D2DA');
        SetIsCreating(false);
    };

    useEffect( () => {
        if (IsCreating) {
            (Text.current as HTMLDivElement).focus();
            (Text.current as HTMLDivElement).style.color = 'black';
        }
    }, [IsCreating])

    return (
        <div
            /*region*/ className={styles.CreatePostElement}
             onDragStart = {e => {e.preventDefault();} }
             onDragLeave = {e => {e.preventDefault();} }
             onDragOver = {e => {e.preventDefault();} }
             onDrop={
                 (event) => {
                     event.stopPropagation();
                     event.preventDefault();
                     if (numberOfFiles.current > 9) return false;
                     /*UploadFile(event.dataTransfer.files[0])
                         .then (res  => {
                             if (res === null || res === undefined) throw new Error();
                             setNumberOfFiles();
                             switch (res.type) {
                                 case 'image': return SetImageFiles(
                                     [...ImageFiles,
                                         {name: res.name.split('.').slice(0, res.name.split('.').length - 1 ).join(''), src: res.src}]);
                                 case 'video': return SetVideoFiles(
                                     [...VideoFiles,
                                         {name: res.name.split('.').slice(0, res.name.split('.').length - 1 ).join(''), src: res.src}]);
                                 case 'music': return SetAudioFiles(
                                     [...AudioFiles,
                                         {name: res.name.split('.').slice(0, res.name.split('.').length - 1 ).join(''), src: res.src}]);
                                 default: return SetOtherFiles(
                                     [...OtherFiles,
                                         {name: res.name, src: res.src}]);
                             }
                         })
                         .catch(() => {return false;} )*/
                 }
             }
        /*endregion*/>
            {IsCreating
                ?
                    <>
                        <div className={GlobalStyles.ModalWindow} style={{backgroundColor: 'rgba(227, 243, 255, .1)'}}
                             onClick={ () => {SetIsCreating(false); PostText = (Text.current as HTMLDivElement).innerText} }/>
                        <div style={{zIndex: '1002', position: 'relative'}}>
                            <div className={styles.CreatePostInput}
                                 style={{marginBottom: '5px', backgroundColor: 'rgba(227, 243, 255, .4)', maxHeight: '500px', overflowY: 'auto'}}//region
                                 onPaste={(e) => {
                                     e.preventDefault();
                                     (Text.current as HTMLDivElement).innerText += e.clipboardData.getData('text/plain');
                                 } }
                                 ref={Text} contentEditable={'true'} suppressContentEditableWarning={true} onKeyUp={TextFilter} onBlur={TextFilterAfterBlur}
                                 /*endregion*/>
                                {PostText}
                            </div>
                            {ImageFiles.length === 0 && AudioFiles.length === 0 && OtherFiles.length === 0 && VideoFiles.length === 0
                                ?
                                    <></>
                                :
                                    <FileBlock ImageList={ImageFiles} AudioList={AudioFiles} OtherList={OtherFiles} VideoList={VideoFiles}/>
                            }
                            <div style={{display: isSurveyCreating ? '' : 'none'}}>
                                <SurveyBLock isSwitch={surveySwitch} background={backgroundColor} setBackgroundColor={SetBackgroundColor} setSurveySwitch={SetSurveySwitch}/>
                            </div>

                            <CreatePostButtonsPanel
                                createPost={createPost}
                                setIsSurveyCreating={setIsSurveyCreating1}
                                numberOfFiles = {numberOfFiles.current}
                                setNumberOfFiles = {setNumberOfFiles}
                                SetVideoFiles={SetVideoFiles1}
                                SetImageFiles={SetImageFiles1}
                                SetAudioFiles={SetAudioFiles1}
                                SetOtherFiles={SetOtherFiles1}
                                ImageFiles={ImageFiles}
                                OtherFiles={OtherFiles}
                                VideoFiles={VideoFiles}
                                AudioFiles={AudioFiles}
                            />
                        </div>
                    </>
                :
                    <div className={styles.CreatePostInput} style={{width: 'calc(100% - 24px)', paddingRight: '8px', overflow: 'hidden'}} onClick={() => {SetIsCreating(true) } }>
                        {PostText === ''
                            ?
                                'Создать новый пост'
                            :
                                PostText
                        }
                    </div>
            }

        </div>
    );

});