import React, {memo, FC, forwardRef, ReactNode} from 'react';
import {FormControl, InputLabel, MenuItem, Select, SelectChangeEvent} from '@mui/material';
import styles from "../../../styles/MainPart.module.scss";

interface Props {
    value: any;
    placeholder: string;
    List: string[];
    SetValue: (event: SelectChangeEvent<any>, child: ReactNode) => void;
}

export const InfoSelect: FC<Props> = memo (({placeholder, value, List, SetValue}) => {
    return (
        <div className={styles.Select}>
            <FormControl fullWidth >
                <InputLabel>{placeholder}</InputLabel>
                <Select/*@ts-ignore*/
                    value={value !== '' ? value : ''}
                    label={placeholder}
                    onChange={SetValue}
                >
                    <MenuItem value={placeholder} style={{display: 'none'}}>{placeholder}</MenuItem>
                    <MenuItem value={'null'}>Оставить это поле пустым</MenuItem>
                    {
                        List.map( (value, index) => {
                            return (
                                <MenuItem key={index} value={value}>{value}</MenuItem>
                            )
                        } )
                    }
                </Select>
            </FormControl>
        </div>
    );

});