import {action, observable, runInAction} from "mobx";
import {CreateAPostDTO, CreateASurveyDTO, createPostFromServer, Post, PostFromServer, Survey} from "../types/PostTypes"
import {hand, RestApiMethods} from "../helper/hand";
import {eye} from "./config";
import {DOMAIN} from "../helper/api";
import {USERID} from "../USERID";
import {FormatDate, GetDateWithCurrentMonth} from "../helper/ParseDate";

interface PostsStateInterface {
    isThereMore: boolean;
    NumberOfPosts: number;
    NumberOfSets: number;
    Posts: Post[];

    readonly getPosts: (AuthorId: number, number: number) => void;
    readonly addPost: (Post: CreateAPostDTO, Survey: CreateASurveyDTO | '', Files: File[]) => void;
}

export const PostState = observable<PostsStateInterface>({
    isThereMore: true, NumberOfPosts: 0, NumberOfSets: 0, Posts: [],

    getPosts: action((AuthorId: number, number: number) => {
        hand('/api/post/' + AuthorId + "?offset=" + (number * 20), RestApiMethods.Get)
            .then (response => {
                if (response?.status === 200) {
                    return response?.json();
                } else {
                    throw new Error(response?.statusText);
                }
            }).then ((Response: {posts: PostFromServer[] | null, surveys: Survey[] | null}) => {
                if (Response.posts === null || Response.posts.length < 1) {
                    runInAction( () => {
                        PostState.NumberOfSets = 1;
                        PostState.isThereMore = false;
                    })
                    throw new Error("No posts");
                }
                return Response as {posts: PostFromServer[] | null, surveys: Survey[] | null}
            }).then( (payload: {posts: any[] | null, surveys: Survey[] | null}) => {
                // @ts-ignore
                if (payload.posts?.length < 20) {
                    runInAction(() => {
                        PostState.isThereMore = false;
                    })
                }
                runInAction( () => {
                    if (payload.posts === null || payload.posts.length < 1) {
                        return;
                    }
                    if (payload.surveys === null || payload.surveys.length < 1) {
                        payload.surveys = []
                    }
                    for (let i = payload.posts.length - 1; i >= 0; i--) {
                        payload.posts[i].date = GetDateWithCurrentMonth(payload.posts[i].date)
                    }
                    PostState.NumberOfSets += 1;
                    PostState.NumberOfPosts += payload.posts.length;
                    for (let i = 0; i < payload.posts.length; i++) {
                        if (payload.posts[i].have_survey) {
                            const survey = payload.surveys.find(survey => survey.parent_post_id === (payload.posts as any[])[i].id) as Survey;
                            payload.posts[i] = createPostFromServer(payload.posts[i], survey);
                        } else {
                            payload.posts[i] = createPostFromServer(payload.posts[i], "");
                        }
                    }
                    PostState.Posts.push(...payload.posts);
                })
            })
    }),

    addPost: (Post, Survey, Files) => {
        const formData = new FormData();
        const PostForJSON = {
            have_survey: Post.HaveASurvey,
            data: Post.Data,
            files: Post.Files,
        }
        formData.append("post", JSON.stringify(PostForJSON));
        if (Survey != "") {
            formData.append("survey", JSON.stringify(Survey));
        }
        for (let i = 0; i < Files.length; i++) {
            formData.append("files", Files[i]);
        }
        const timer = Date.now();
        eye.fetchSend(DOMAIN + "/api/post/", RestApiMethods.Post)
        fetch(DOMAIN + "/api/post/", {
            method: RestApiMethods.Post,
            credentials: 'include',
            body: formData,
        }).then(response => {
            eye.fetchGet(DOMAIN + "/api/post/", RestApiMethods.Post, response.status, Date.now() - timer)
            if (response.status === 201) {
                runInAction(() => {
                    PostState.Posts.unshift({
                        id: -1,
                        dislikes: 0,
                        likes: 0,
                        disliked_by: [],
                        liked_by: [],
                        files: Post.Files,
                        data: Post.Data,
                        parentUserID: +USERID,
                        date: 'Только что',
                        survey: Survey === "" ? null :
                            {
                                    sl0v: 0, sl1v: 0, sl2v: 0, sl3v: 0, sl4v: 0, sl5v: 0, sl6v: 0, sl7v: 0, sl8v: 0, sl9v: 0,
                                    sl0vby: [+USERID], sl1vby: [+USERID], sl2vby: [+USERID], sl3vby: [+USERID], sl4vby: [+USERID],
                                    sl5vby: [+USERID], sl6vby: [+USERID], sl7vby: [+USERID], sl8vby: [+USERID], sl9vby: [+USERID],
                                    data: Survey.Data, parent_post_id: -1, is_multi_voices: Survey.is_multi_voices, background: Survey.Background, votedBy: [+USERID]
                            },
                        haveASurvey: Survey === ""
                    })
                })
            }
        }).catch((reason) => {
            eye.fetchGet(DOMAIN + "/api/post/", RestApiMethods.Post, 500, Date.now() - timer)
        })
    }
})