import React, {memo, FC} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {Post as PostType} from "../../../types/PostTypes";
import {Card} from "@mui/material";
import {SurveyBlockInSitePost} from "../../AI/Post/SurveyBlockInSitePost";
import {FileBlockInPost} from "./FileBlockInPost";
import { PostBottom } from '../../AI/Post/PostBottom';

interface Props extends PostType {
    IsFriend: boolean | NodeJS.Timer;
    /** This is an array that has number to create ID`s for music*/
    MusicNumbers: number[];
    music_files: string[];
    video_files: string[];
    image_files: string[];
    other_files: string[];
}

export const Post: FC<Props> = memo (({id,
    date, survey, data, dislikes, disliked_by, liked_by, other_files, image_files, music_files, video_files, haveASurvey,
    likes,  MusicNumbers
}) => {
    // todo
    // if (VisibleBy === 'Friends' && IsFriend !== true) {
    //     return <></>;
    // }

    return (
        <Card className={styles.Post} style={{backgroundColor: 'rgba(0, 0, 0, .2)'}}>
            <div className={styles.top}>Пост был опубликован {date}</div>
            <div className={styles.text}>{data}</div>
            <FileBlockInPost MusicNumbers={MusicNumbers} music_files={music_files} image_files={image_files} other_files={other_files} video_files={video_files} id={id}/>
            {haveASurvey && survey != null && survey != "" && survey.data.length > 1
                ?
                    <SurveyBlockInSitePost id={id} survey={survey}/>
                :
                    <></>
            }
            <PostBottom id={id} likes={likes} dislikes={dislikes} likedBy={liked_by} dislikedBy={disliked_by}/>
        </Card>
    );

});