import React, {FC, memo, useCallback, useEffect, useMemo, useRef, useState} from 'react';
import {Comment as CommentType} from "../../../types/PostTypes";
import styles from "../../../styles/MainPart.module.scss";
import ThumbUpIcon from "@mui/icons-material/ThumbUp";
import ThumbDownIcon from "@mui/icons-material/ThumbDown";
import {LazyImg} from "../../../helper/LazyImg";
import {DOMAIN} from "../../../helper/api";
import {CommentStore} from "../../../store/CommentStore";
import DeleteIcon from "@mui/icons-material/Delete";
import {PageId} from "../../../helper/GetPage";
import {USERID} from "../../../USERID";
import {User, UsersStore} from "../../../store/UsersStore";

interface Props extends CommentType {
}

export const Comment: FC<Props> = memo(({
            id, parent_user_id, files, liked_by, likes, disliked_by, dislikes, date, data, parent_comment_id, parent_post_id
        }) => {

    const estimationOnStart = useRef<'liked' | 'disliked' | 'none' | 'author'>(parent_user_id == +USERID ? 'author' :
        liked_by.indexOf(+USERID) !== -1 ? 'liked' : disliked_by.indexOf(+USERID) !== -1 ? 'disliked' : 'none');
    const [estimation, setEstimation] = useState<'liked' | 'disliked' | 'none' | 'author'>(estimationOnStart.current)

    const like = useRef<HTMLDivElement>(null),
        dislike = useRef<HTMLDivElement>(null);

    const likeThis = useCallback(() => {
        if (estimationOnStart.current === 'author') return;
        if (estimation !== 'liked') {
            if (estimation === 'disliked' || disliked_by.indexOf(+USERID) !== -1) {
                CommentStore.undislike(id)
                    .then( () => {CommentStore.like(id)});
            } else {
                CommentStore.like(id);
            }
            setEstimation('liked');
        } else {
            CommentStore.unlike(id);
            setEstimation('none');
        }
    }, [estimation]);

    const dislikeThis = useCallback(() => {
        if (estimationOnStart.current === 'author') return;
        if (estimation !== 'disliked') {
            if (estimation === 'liked' || liked_by.indexOf(+USERID) !== -1) {
                CommentStore.unlike(id)
                    .then( () => {CommentStore.dislike(id)});
            } else {
                CommentStore.dislike(id);
            }
            setEstimation('disliked');
        } else {
            CommentStore.undislike(id);
            setEstimation('none');
        }
    }, [estimation])

    useEffect(() => {
        switch (estimation) {
            case "liked":
                (dislike.current as HTMLDivElement).style.backgroundColor = '';
                (like.current as HTMLDivElement).style.backgroundColor = 'rgba(255, 255, 255, 0.3)';
                break;
            case "disliked":
                (like.current as HTMLDivElement).style.backgroundColor = '';
                (dislike.current as HTMLDivElement).style.backgroundColor = 'rgba(255, 255, 255, 0.3)';
                break;

            default:
                (like.current as HTMLDivElement).style.backgroundColor = '';
                (dislike.current as HTMLDivElement).style.backgroundColor = '';
                return;
        }
    }, [estimation]);

    const owner = useMemo(() => {
        return UsersStore.users.find(user => user.ID === parent_user_id) as User;
    }, [])

    return (
        <div className={styles.commentLine}>
            <div className={styles.top}>{parent_user_id}{date}</div>
            <div className={styles.main}>
                <LazyImg Onclick={() => {
                    document.location.href = DOMAIN + '/pages/profile/?userId=' + parent_user_id
                }} DivToImageStyles={{height: '100%', display: 'inline-block'}}
                         ImageStyles={{width: '40px', borderRadius: '50%', cursor: 'pointer', marginRight: '5px', height: '40px'}}
                         element={
                             <div className={styles.miniAvatar}>
                                 <div></div>
                                 <div></div>
                                 <div></div>
                                 <div></div>
                                 <div></div>
                                 <div></div>
                                 <div></div>
                                 <div></div>
                                 <div></div>
                                 <div></div>
                                 <div></div>
                                 <div></div>
                             </div>
                         }
                         src={DOMAIN + (owner.Avatar.split('/')[owner.Avatar.split('/').length - 1] == '' ? '/UserFiles/NULL.png' : '/UserFiles/' + owner.ID + '/Image/' + owner.Avatar)}/>
                <div className={styles.text}>
                    {data}
                </div>
            </div>
            <div style={{position: 'relative', marginTop: '10px', display: 'flex', justifyContent: 'space-between'}}>
                <div className={styles.likesAndDislikesBlock}>
                    <div ref={like} onClick={likeThis} className={styles.estimationBlock + ' ' + styles.likes}>
                        <ThumbUpIcon style={{fill: 'white'}}/>
                        {likes}
                    </div>
                    <div ref={dislike} onClick={dislikeThis} className={styles.estimationBlock + ' ' + styles.dislikes}>
                        <ThumbDownIcon style={{fill: 'white'}}/>
                        {dislikes}
                    </div>
                </div>
                {parent_user_id === +USERID
                    ?
                    <DeleteIcon className={styles.trash} onClick={
                        () => {
                            CommentStore.delete(id);
                        }
                    }/>
                    :
                    <></>
                }
            </div>
        </div>
    );

});