import React, {FC, useEffect} from 'react';
import styles from '../../../styles/MainPart.module.scss';
import {Avatar} from "../../UI/Info/Avatar";
import {OwnerPanel} from "../../UI/Info/OwnerPanel";
import {NotOwnerPanel} from "../../UI/Info/NotOwnerPanel";
import {InfoStore} from "../../../store/InfoStore";
import {FriendStatusStore} from "../../../store/FriendStatusStore";
import {observer} from "mobx-react-lite";

interface Props {
    SetIsChange?: Function;
    IsOwner: boolean;
}

export const LeftColumn: FC<Props> = observer(({SetIsChange, IsOwner}) => {

    useEffect( () => {
        if (document.getElementById("PrevLoad") !== null) {
            InfoStore.getInfo();
            (document.getElementById("PrevLoad") as HTMLElement).remove();
        }
    }, []);

    if (InfoStore.name !== null && FriendStatusStore.StateType != 'first') {
        if ((document.getElementById("DocumentTitle") as HTMLElement).textContent === "Профиль"
            || (document.getElementById("DocumentTitle") as HTMLElement).textContent === "null null") {// @ts-ignore I check it above
            (document.getElementById("DocumentTitle") as HTMLElement).textContent
                = InfoStore.name + ' '  + InfoStore.surname;
        }
        return (
            <div className={styles.LeftColumn}>{/*@ts-ignore*/}
                <Avatar Src={InfoStore.avatar} ImgStyles={{width: '240px', height: '240px'}}/>
                {
                    IsOwner
                        ?
                            <OwnerPanel SetIsChange={SetIsChange}/>
                        :
                            <NotOwnerPanel IsFriendStore={FriendStatusStore.StateType}/>
                }
            </div>
        );
    } else {
        return (
            <div className={"LeftColumn"}>
                <div>
                    <div className="PrevAvatar"></div>
                    <div className="PrevButtonInLeftColumn"></div>
                </div>
            </div>
        );
    }

});