import React, {memo, FC, useCallback} from 'react';
import {ImageList, ImageListItem, ImageListItemBar} from "@mui/material";
import classes from "../../../styles/HeaderAndMusicPanelStyles.module.css";
import {DOMAIN} from "../../../helper/api";
import {MusicStore} from "../../../LayOut/store/stores/MusicStore";
import {VideoStore} from "../../../LayOut/store/stores/VideoStore";
import {ImageStore} from "../../../LayOut/store/stores/ImageStore";
import {PageId} from "../../../helper/GetPage";

const PauseSong = (number: number) => {
    MusicStore.pause(false, number + 1)
};

const PlaySong = (number: number, src: string, name: string) => {
    MusicStore.start(name, src, number - 1, false, true);
};

export const FileBlockInPost: FC<{ id: number,music_files: string[], image_files: string[], other_files: string[], video_files: string[], MusicNumbers: number[]}> = memo (
        ({video_files, other_files, image_files, music_files, id, MusicNumbers
    }) => {

    const onVideoClick = useCallback ((src: string, name: string) => {
        VideoStore.openNewVideo(name, src);
    }, []);
    const onImageClick = useCallback ((src: string, name: string) => {
        ImageStore.openNewImage(name, src);
    }, []);

    try {
        return (
            <div>
                {image_files !== null && image_files.length > 0 || video_files !== null && video_files.length > 0
                    ?
                    <ImageList sx={{width: '100%'}}>
                        <>
                            {image_files !== null
                                ? image_files.map( (value, index) => {
                                    return (
                                        <ImageListItem
                                            sx={{cursor: 'pointer'}}
                                            key={'ImageInPost' + id + 'index' + index}
                                            onClick={() => {onImageClick(DOMAIN + '/UserFiles/' + PageId+ '/image/' + value, value) } }
                                        >
                                            <img src={DOMAIN + '/UserFiles/' + PageId+ '/image/' + value}
                                                 loading={'lazy'} alt={'Картинка не загрузилась'}/>
                                            <ImageListItemBar subtitle={value} style={{padding: '0px'}}/>
                                        </ImageListItem>
                                    )
                                })
                                :
                                <></>
                            }
                            {
                                video_files !== null && video_files.length > 0
                                    ?
                                    video_files.map( (value, index) => {
                                        return (
                                            <ImageListItem
                                                sx={{cursor: 'pointer'}}
                                                key={'VideoInPost' + id + 'index' + index}
                                                onClick={() => {onVideoClick(DOMAIN + '/UserFiles/' + PageId + '/video/' + value, value)} }
                                            >
                                                <video style={{maxWidth: '249px', maxHeight: '140px'}} src={DOMAIN + '/UserFiles/' + PageId + '/video/' + value}/>
                                                <div style={{position: 'relative', top: '-70px', left: '110px', width: '30px'}}>
                                                    <svg width={'30px'} viewBox="0 0 100 100">
                                                        <g>
                                                            <path
                                                                d="M31.356,25.677l38.625,22.3c1.557,0.899,1.557,3.147,0,4.046l-38.625,22.3c-1.557,0.899-3.504-0.225-3.504-2.023V27.7   C27.852,25.902,29.798,24.778,31.356,25.677z"/>
                                                            <path
                                                                d="M69.981,47.977l-38.625-22.3c-0.233-0.134-0.474-0.21-0.716-0.259l37.341,21.559c1.557,0.899,1.557,3.147,0,4.046   l-38.625,22.3c-0.349,0.201-0.716,0.288-1.078,0.301c0.656,0.938,1.961,1.343,3.078,0.699l38.625-22.3   C71.538,51.124,71.538,48.876,69.981,47.977z"/>
                                                            <path
                                                                d="M31.356,25.677l38.625,22.3c1.557,0.899,1.557,3.147,0,4.046   l-38.625,22.3c-1.557,0.899-3.504-0.225-3.504-2.023V27.7C27.852,25.902,29.798,24.778,31.356,25.677z"
                                                                style={{
                                                                    fill: '#ffffff',
                                                                    stroke: '#ffffff',
                                                                    strokeMiterlimit: '10'
                                                                }}/>
                                                        </g>
                                                    </svg>
                                                </div>
                                                <ImageListItemBar subtitle={value} style={{padding: '0px'}}/>
                                            </ImageListItem>
                                        )
                                    })
                                    :
                                    <></>
                            }
                        </>
                    </ImageList>
                    :
                    <></>
                }
                {music_files !== null && music_files.length > 0
                    ?
                    music_files.map( (value, index) => {
                        return (
                            <div key={index}>
                                <div className={classes.MusicListMusicDiv} style={{top: '0px'}}>
                                    <div className={classes.MusicListMusicLine}>
                                        <div className={classes.BlockToButtonPlayOrPause}>
                                            <div className={classes.ButtonPlayOrPauseSong}>
                                                <svg
                                                    style={{display: MusicStore.number === MusicNumbers[index] && MusicStore.isPlaying ? 'inline-block' : 'none', left: '10px', top: '7px'}}
                                                    id={`ButtonPauseOFSongInPost${MusicNumbers[index]}`}
                                                    className={classes.imgButtonPlayOrPause}
                                                    onClick={() => {
                                                        PauseSong(MusicNumbers[index]);
                                                    }} viewBox="30 30 100 100">
                                                    <g>
                                                        <path
                                                            d="M39.806,72.858h-8.915c-2.176,0-3.94-1.764-3.94-3.94V31.119c0-2.176,1.764-3.94,3.94-3.94h8.915   c2.176,0,3.94,1.764,3.94,3.94v37.799C43.746,71.094,41.982,72.858,39.806,72.858z"/>
                                                        <path
                                                            d="M68.109,72.821h-8.915c-2.176,0-3.94-1.764-3.94-3.94V31.082c0-2.176,1.764-3.94,3.94-3.94h8.915   c2.176,0,3.94,1.764,3.94,3.94v37.799C72.049,71.057,70.285,72.821,68.109,72.821z"/>
                                                        <path
                                                            d="M40.489,27.248c0.769,0.719,1.257,1.735,1.257,2.871v37.799c0,2.176-1.764,3.94-3.94,3.94h-8.915   c-0.234,0-0.46-0.03-0.683-0.069c0.704,0.658,1.643,1.069,2.683,1.069h8.915c2.176,0,3.94-1.764,3.94-3.94V31.119   C43.746,29.177,42.338,27.573,40.489,27.248z"/>
                                                        <path
                                                            d="M68.792,27.211c0.769,0.719,1.257,1.735,1.257,2.871v37.799c0,2.176-1.764,3.94-3.94,3.94h-8.915   c-0.234,0-0.46-0.03-0.683-0.069c0.704,0.658,1.643,1.069,2.683,1.069h8.915c2.176,0,3.94-1.764,3.94-3.94V31.082   C72.049,29.14,70.641,27.535,68.792,27.211z"/>
                                                        <path
                                                            d="M39.806,72.858h-8.915c-2.176,0-3.94-1.764-3.94-3.94V31.119   c0-2.176,1.764-3.94,3.94-3.94h8.915c2.176,0,3.94,1.764,3.94,3.94v37.799C43.746,71.094,41.982,72.858,39.806,72.858z"
                                                            style={{fill: '#ffffff', stroke: '#ffffff', strokeMiterlimit: '10'}}/>
                                                        <path
                                                            d="M68.109,72.821h-8.915c-2.176,0-3.94-1.764-3.94-3.94V31.082   c0-2.176,1.764-3.94,3.94-3.94h8.915c2.176,0,3.94,1.764,3.94,3.94v37.799C72.049,71.057,70.285,72.821,68.109,72.821z"
                                                            style={{fill: '#ffffff', stroke: '#ffffff', strokeMiterlimit: '10'}}/>
                                                    </g>
                                                </svg>
                                                <svg viewBox="0 0 80 80" id={`ButtonPlayOFSongInPost${MusicNumbers[index]}`} className={classes.imgButtonPlayOrPause}
                                                     style={{display: MusicStore.number === MusicNumbers[index] && MusicStore.isPlaying ? 'none' : 'inline-block',width:"30px", height:"24px", top:"-3px"}}
                                                     onClick={() => {PlaySong(MusicNumbers[index], PageId+'/Music/' + value, value)}}>
                                                    <g>
                                                        <path d="M31.356,25.677l38.625,22.3c1.557,0.899,1.557,3.147,0,4.046l-38.625,22.3c-1.557,0.899-3.504-0.225-3.504-2.023V27.7   C27.852,25.902,29.798,24.778,31.356,25.677z"/>
                                                        <path d="M69.981,47.977l-38.625-22.3c-0.233-0.134-0.474-0.21-0.716-0.259l37.341,21.559c1.557,0.899,1.557,3.147,0,4.046   l-38.625,22.3c-0.349,0.201-0.716,0.288-1.078,0.301c0.656,0.938,1.961,1.343,3.078,0.699l38.625-22.3   C71.538,51.124,71.538,48.876,69.981,47.977z"/>
                                                        <path d="M31.356,25.677l38.625,22.3c1.557,0.899,1.557,3.147,0,4.046   l-38.625,22.3c-1.557,0.899-3.504-0.225-3.504-2.023V27.7C27.852,25.902,29.798,24.778,31.356,25.677z"
                                                              style={{fill: '#ffffff', stroke: '#ffffff', strokeMiterlimit: '10'}}/>
                                                    </g>
                                                </svg>
                                            </div>
                                        </div>
                                        <div className={classes.SongNameInMusicListMusicLine} style={{width: '465px'}}>{value}</div>
                                    </div>
                                </div>
                            </div>
                        )
                    })
                    :
                    <></>
                }
                {other_files !== null && other_files.length > 0
                    ?
                    other_files.map( (value, index) => {
                        return (
                            <a style={{display: 'block', paddingTop: '5px'}} key={index} download={true} href={DOMAIN + '/UserFiles/' + PageId+ '/Other/' + value}>
                                {value}
                            </a>
                        );
                    })
                    :
                    <></>
                }
            </div>
        );
    } catch (e) {
        return <></>
    }
});