import {hand, RestApiMethods} from "../../../helper/hand";
import {eye} from "../../config";

export const VoteInSurveyFetch = (postId: number, VotedFor: number[]) => {
    return hand(`/api/post/${postId}/survey`, RestApiMethods.Patch, JSON.stringify ({voted_for: VotedFor}))
        .then (response => {
            if (response?.status !== 204) {
                eye.warn(`Failed to vote in survey in post ${postId}!`);
            }
        })
}