import React, {memo, FC, useEffect, useCallback, useState, useRef} from 'react';
import {CreatePostButton} from "../../UI/CreatePost/CreatePostButton";
import {showingTo} from "./CreatePostElement";
//import {UploadFile} from "../../../helper/UploadFile";

interface Props {
    SetAudioFiles: any;
    SetVideoFiles: any;
    SetImageFiles: any;
    SetOtherFiles: any;
    AudioFiles: any;
    VideoFiles: any;
    ImageFiles: any;
    numberOfFiles: number;
    OtherFiles: any;
    setNumberOfFiles: any;
    setIsSurveyCreating: any;
    createPost: () => void;
    SetSurveySwitch1?: any;
    SetSurveySwitch2?: any;
}

export const CreatePostButtonsPanel: FC<Props> = memo (({
    createPost,
    SetAudioFiles,
    SetOtherFiles,
    SetVideoFiles,
    SetImageFiles,
    AudioFiles,
    ImageFiles,
    OtherFiles,
    numberOfFiles,
    VideoFiles,
    setNumberOfFiles,
    setIsSurveyCreating
}) => {

    const [IsShowingHint, setIsShowingHint] = useState(false);
    const [isChooseVision, setIsChooseVision] = useState (false);

    const SetIsShowingHintCB = useCallback ((value: boolean) => {
        setIsShowingHint(value);
    }, []);

    const Block = useRef<HTMLDivElement>(null);

    const FileInput = useRef<HTMLInputElement> (null);
    
    const AddFile = useCallback(() => {
        if (FileInput.current !== null && FileInput.current.files !== null) {
            if (numberOfFiles > 9) return false;
            /*UploadFile(FileInput.current.files[0])
                .then (res  => {
                    if (res === null || res === undefined) throw new Error();
                    setNumberOfFiles();
                    switch (res.type) {
                        case 'image': return SetImageFiles(
                            [...ImageFiles, {name: res.name.split('.').slice(0, res.name.split('.').length - 1 ).join(''), src:res.src} ]);
                        case 'video': return SetVideoFiles(
                            [...VideoFiles, {name: res.name.split('.').slice(0, res.name.split('.').length - 1 ).join(''), src:res.src} ]);
                        case 'music': return SetAudioFiles(
                            [...AudioFiles, {name: res.name.split('.').slice(0, res.name.split('.').length - 1 ).join(''), src:res.src} ]);
                        default: return SetOtherFiles(
                            [...OtherFiles, {name: res.name, src:res.src} ]);
                    }
                })
                .catch(() => {return false;} )*/
        }
    }, [AudioFiles, ImageFiles, VideoFiles, OtherFiles]);

    const Click = useCallback (() => {
        // @ts-ignore
        FileInput.current.value = '';
        // @ts-ignore
        FileInput.current.click()
    }, []);
    /**
     * value: boolean, true - vision for all, false - vision for friends only.
     */
    const setIsVisionToAll = useCallback ((value: boolean) => {
        showingTo.current = value ? 'All' : 'Friends';
        setIsChooseVision(false);
    }, []);

    useEffect (() => {
        if (isChooseVision) {
            (Block.current as HTMLDivElement).style.paddingBottom = '0';
        } else {
            if (IsShowingHint) {
                (Block.current as HTMLDivElement).style.paddingBottom = '32px';
            } else {
                (Block.current as HTMLDivElement).style.paddingBottom = '0';
            }
        }
    }, [IsShowingHint, isChooseVision]);

    return (
        <div ref={Block}>
            <input type={'file'} ref={FileInput} onChange={AddFile} style={{display: 'none'}} />
            <CreatePostButton WhatIsIt={'clip'} Function={Click} SetHint={SetIsShowingHintCB}/>
            <CreatePostButton WhatIsIt={'survey'} Function={() => {setIsSurveyCreating()} } SetHint={SetIsShowingHintCB}/>
            <CreatePostButton WhatIsIt={'eye'} Function={() => {setIsChooseVision(!isChooseVision)} } SetHint={SetIsShowingHintCB}/>
            <CreatePostButton WhatIsIt={'create'} Function={createPost}/>
            {isChooseVision
                ?
                    <div style={{position: 'relative', height: '40px'}}>
                        <div style={{position: 'absolute',  width: '200px', left: '110px', top: '-30px'}}>
                            <div style={{padding: '5px', backgroundColor: 'rgba(170, 170, 170)', cursor: 'pointer',
                                borderRadius: '5px 5px 0 0', borderBottom: 'white 1px solid'}}
                                onClick={() => {setIsVisionToAll(true)} }>
                                Для всех
                                {showingTo.current === 'Friends'
                                    ?
                                        ''
                                    :
                                        <div style={{display: 'inline-block', position: 'relative', left: '100px'}}>✓</div>
                                }
                            </div>
                            <div style={{padding: '5px', backgroundColor: 'rgba(170, 170, 170)', cursor: 'pointer',
                                borderRadius: '0 0 5px 5px'}}
                                onClick={() => {setIsVisionToAll(false)} }>
                                Только для друзей
                                {showingTo.current === 'Friends'
                                    ?
                                        <div style={{display: 'inline-block', position: 'relative', left: '20px'}}>✓</div>
                                    :
                                        ''
                                }
                            </div>
                        </div>
                    </div>
                :
                    <></>
            }
        </div>
    );

});