import React, {memo, FC, useState} from 'react';
import GlobalStyles from '../../../LayOut/styles/GlobalStyles.module.scss';
import styles from '../../../styles/MainPart.module.scss';
import {Avatar} from "../../UI/Info/Avatar";
import {ChangeAvatar} from "./ChangeAvatar";
import {ChangeRight} from "./ChangeRight";
import {InfoStore} from "../../../store/InfoStore";
import {observer} from "mobx-react-lite";

interface Props {
    SetIsChange: Function;
}

export const ChangeMenu: FC<Props> = observer(({SetIsChange}) => {

    const [IsAvatarChange, SetIsAvatarChange] = useState<boolean>(false);

    return (
        <div>
            {//region
                IsAvatarChange
                ?
                    <>
                        <div className={GlobalStyles.ModalWindow}/>
                        <ChangeAvatar SetIsAvatarChange={SetIsAvatarChange}/>
                    </>
                :
                    <></>
            /*endregion*/}
            <div className={'LeftColumn'} style={{height: '275px'}}>
                <div>{/*@ts-ignore*/}
                    <Avatar Src={InfoStore.avatar} ImgStyles={{width: '240px', height: '240px'}}/>
                    <div className={'PrevButtonInLeftColumn'}>
                        <div className={styles.PrevButtonInLeftColumn_Text} onClick={
                            () => {
                                SetIsAvatarChange(true);
                            }
                        }>
                            Изменить аватар
                        </div>
                    </div>
                </div>
            </div>
            <div className={styles.ProfileInfo} id={'ChangeMenu_InfoBlock'} style={{left: '256px', width: '517px'}}>
                <ChangeRight SetIsChange={SetIsChange}/>
            </div>
        </div>
    );

});