import React, {memo, FC, useState, useRef, useEffect, forwardRef} from 'react';
import styles from '../../../styles/MainPart.module.scss'
import {scryRenderedComponentsWithType} from "react-dom/test-utils";

interface Props {
    ref: any
    placeholder: string;
    value: string | null;
}

export const InfoDate: FC<Props> = memo (forwardRef(({placeholder, value}, ref) => {

    const [Value, SetValue] = useState(value);
    const Input = useRef<HTMLInputElement>(null);

    const OnClick = () => {
        (Input.current as HTMLInputElement).click();
    },
    OnChange = () => {
        const Value = (Input.current as HTMLInputElement).value.toString().split('-'), NewValue = [];
        NewValue.push(Value[2]);
        switch (Value[1]) {
            case '01':
                NewValue.push('января');
                break;
            case '02':
                NewValue.push('февраля');
                break;
            case '03':
                NewValue.push('марта');
                break;
            case '04':
                NewValue.push('апреля');
                break;
            case '05':
                NewValue.push('мая');
                break;
            case '06':
                NewValue.push('июня');
                break;
            case '07':
                NewValue.push('июля');
                break;
            case '08':
                NewValue.push('августа');
                break;
            case '09':
                NewValue.push('сентября');
                break;
            case '10':
                NewValue.push('октября');
                break;
            case '11':
                NewValue.push('ноября');
                break;
            case '12':
                NewValue.push('декабря');
                break;
        }
        NewValue.push(Value[0]);
        // @ts-ignore
        document.getElementById('TextInInfoDate').innerText = NewValue.join(' ');
    };

    useEffect( () => {
        // @ts-ignore
        document.getElementById('TextInInfoDate').innerText = Value;
    }, []);

    return (
        <div className={styles.InfoInput} onClick={OnClick} style={{height: '29px', marginTop: '25px'}}>
            <div className={styles.TextAbove} style={{position: 'absolute', top: '-24px'}}>
                {placeholder}
            </div>
            {/*@ts-ignore*/}
            <input ref={Input} type={"date"} onChange={OnChange}
                   style={{display: 'block', width: '100%', height: '100%', fontSize: '20px', outline: 'none', border: 'none',
                       position: 'absolute', top: '0', left: '0', backgroundColor: 'rgba(0, 0, 0, .0)'}}/>{/*@ts-ignore*/}
            <div ref={ref} id={'TextInInfoDate'} style={{display: 'none'}}></div>
        </div>
    );

}));