import React, {memo, FC, useCallback, useRef, useState} from 'react';
import InputStyles from '../../../helper/Input.module.scss';
import styles from '../../../styles/MainPart.module.scss';
import {FormControlLabel, Switch} from '@mui/material';
import {SurveyLine} from "../../UI/CreatePost/Survey/SurveyLine";
import {surveyLines} from "./CreatePostElement";
import {BackgroundColorBlock} from "../../UI/CreatePost/Survey/BackgroundColorBlock";
import {USERID} from "../../../USERID";

interface Props {
    setSurveySwitch: any;
    setBackgroundColor: any;
    background: string;
    isSwitch: boolean;
}

export const SurveyBLock: FC<Props> = memo ( ({setSurveySwitch, setBackgroundColor, background, isSwitch}) => {

    const [surveyLines2, setSurveyLines2] = useState (JSON.parse(JSON.stringify(surveyLines) ) );
    const Inputs = [useRef(null), useRef(null), useRef(null), useRef(null), useRef(null), useRef(null),
        useRef(null), useRef(null), useRef(null), useRef(null)];//@ts-ignore
    const surveyBlock = useRef<HTMLDivElement> (null);

    const switchChange = useCallback ((event: React.ChangeEvent<HTMLInputElement>) => {
        setSurveySwitch(event.target.checked);
    }, []);

    const cross =(number: number) => {
        surveyLines.splice(number, 1);
        setSurveyLines2( JSON.parse(JSON.stringify(surveyLines) ) );
    };

    const NewLine = useCallback(() => {
        // @ts-ignore
        surveyLines.push('');
        setSurveyLines2(JSON.parse(JSON.stringify(surveyLines) ));
    }, [surveyLines2, surveyLines]);

    const onChange = useCallback( (number: number, text: string) => {
        surveyLines[number] = text;
    }, [surveyLines2, surveyLines]);

    const changeBackground = useCallback ((color: string) => {
        setBackgroundColor(color);
    }, [surveyBlock]);

    return (
        <div className={styles.SurveyCreateBlock} style={{background: background}} ref={surveyBlock}>
            <div>
                {
                    surveyLines.map( (value, index) => {
                        if (index === surveyLines.length - 1 && index > 1) {
                            return <SurveyLine value={surveyLines[index]} withCross={true} number={index}  onChange={onChange} onClick={cross} ref={Inputs[index]} label={index + 1 + ' вариант ответа'} key={index}/>
                        }
                         else {
                            return <SurveyLine value={surveyLines[index]} number={index}  onChange={onChange} onClick={cross} ref={Inputs[index]} label={index + 1 + ' вариант ответа'} key={index}/>;
                        }
                    })
                }
                {surveyLines.length < 10
                    ?
                        <div className={InputStyles.Input + ' ' + styles.lastLine} onClick={NewLine}>
                            <div className={styles.text}>Новый вариант ответа</div>
                        </div>
                    :
                        <></>
                }
            </div>
            <div style={{position: 'relative', left: '15px', width: '450px', margin: '8px 0', fontSize: '20px'}}>
                Выберите тему
            </div>
            <div>
                <div style={{position: 'relative', left: '15px', width: '450px'}}>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'#C7D2DA'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(136deg, #fffafa 0%,#7fc7ff 100%)'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(136deg, #fd7c6e 0%,#8000ff 100%)'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(136deg, #00a550 0%,#ffa500 100%)'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(136deg, #00a550 20%,#6600ff 80%)'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(136deg, #42aaff 0%,#6600ff 100%)'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(136deg, #31408c 0%,#526fcc 100%)'}/>
                </div>
                <div style={{position: 'relative', left: '15px', width: '450px'}}>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(97deg, #707c8c 0%,#4d5565 100%)'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(123deg, #d93650 0%,#d99817 100%)'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(96deg, #679945 0%,#2f733f 100%)'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(95deg, #ffa812 0%,#fefe22 100%)'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(132deg, #2eb3b3 0%,#2552b3 100%)'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(133deg, #ff4252 0%,#6600ff 100%)'}/>
                    <BackgroundColorBlock onClick={changeBackground} backgroundColor={'linear-gradient(170deg, #2aa691 0%,#13806d 100%)'}/>
                </div>
            </div>
            <div className={styles.switchBlock}>
                <FormControlLabel
                    checked={isSwitch}
                    control={<Switch onChange={switchChange} color="primary"/>}
                    label="Множество ответов"
                    labelPlacement="start"
                />
            </div>
        </div>
    );

});