import React, {memo, FC, useState, useCallback, useRef, useMemo} from 'react';
import {SurveyInSiteLine} from "../../UI/Post/SurveyInSiteLine";
import styles from '../../../styles/MainPart.module.scss';
import {VoteInSurveyFetch} from "../../../store/fetch/Posts/VoteInSurvey";
import {PageId} from "../../../helper/GetPage";
import {USERID} from "../../../USERID";
import {Survey} from "../../../types/PostTypes";

const getVotedFor = (slNVby: number[][], userId: number, IsMultipleChoice: boolean): number | number[] | false => {
    let result: number | number[] | false = false;
    if (IsMultipleChoice) {
        result = [];
        slNVby.map( (value, index) => {
            if (value.indexOf(userId) !== -1) {
                (result as number[]).push(index);
            }
        });
        if (result.length === 0) return []
    } else {
        slNVby.map( (value, index) => {
            if (value.indexOf(userId) !== -1) {
                result = index;
            }
        });
    }
    return result;
}

interface Props {
    survey: Survey,
    id: number
}

export const SurveyBlockInSitePost: FC<Props> = memo(({survey, id}) => {

    const Survey: Survey = survey;

    /** if in first render votedFor === true*/
    const isFirstChosen = useRef ( (() => {
        let current = getVotedFor([Survey.sl0vby, Survey.sl1vby, Survey.sl2vby, Survey.sl3vby, Survey.sl4vby, Survey.sl5vby, Survey.sl6vby, Survey.sl7vby, Survey.sl8vby, Survey.sl9vby], +USERID, Survey.is_multi_voices);
        if (!Survey.is_multi_voices) {
            return current !== false ? current as number : -1;
        } else {
            return (current as number[]).length > 0;
        }
    })());
    const SurveyLinesVotedby = useMemo<number[][]>(() => {
        return [survey.sl0vby, survey.sl1vby, survey.sl2vby, survey.sl3vby, survey.sl4vby, survey.sl5vby, survey.sl6vby, survey.sl7vby, survey.sl8vby, survey.sl9vby];
    }, [survey.sl0v, survey.sl0vby, survey.sl1v, survey.sl1vby, survey.sl2v, survey.sl2vby, survey.sl3v, survey.sl3vby, survey.sl4v, survey.sl4vby, survey.sl5v, survey.sl5vby, survey.sl6v, survey.sl6vby, survey.sl7v, survey.sl7vby, survey.sl8v, survey.sl8vby, survey.sl9v, survey.sl9vby]);
    const [votedFor, setVotedFor] = useState<number | number[] | false> (getVotedFor(SurveyLinesVotedby, +USERID, Survey.is_multi_voices)),
    isChosen = useRef(isFirstChosen.current !== false && isFirstChosen.current !== -1), [render, setRender] = useState(false);
    
    const setVotedFor1 = useCallback ((index: number) => {
        if (Survey.is_multi_voices) {
            if (isChosen.current) return;
            const copy = [...votedFor as number[]];
            if (copy.indexOf(index) === -1) {
                copy.push(index);
            } else {
                copy.splice(copy.indexOf(index), 1);
            }
            setVotedFor(copy);
        } else {
            if (!isChosen.current) {
                setVotedFor(index);
                isChosen.current = true;
                VoteInSurveyFetch(id, [index as number]);
            }
        }
    }, [votedFor]);
    
    return (
        <div className={styles.surveyBlock} style={{background: Survey.background}}>
            {
                survey.data.map( (value, index) => {
                    let isItChosen: boolean;
                    if (Survey.is_multi_voices) {
                        isItChosen = (votedFor as number[]).indexOf(index) !== -1;
                    } else {
                        isItChosen = votedFor === index;
                    }
                    switch (index) {
                        case 0: {
                            return (
                                <SurveyInSiteLine key={index} index={index} IsMultipleChoice={Survey.is_multi_voices} setChosen={setVotedFor1}
                                                  isChosen={isChosen.current} isItChosen={isItChosen} isFirstChosen={isFirstChosen.current > -1}
                                                  numberOfVoice={survey.sl0v} text={value}
                                />
                            );
                        }
                        case 1: {
                            return (
                                <SurveyInSiteLine key={index} index={index} IsMultipleChoice={Survey.is_multi_voices} setChosen={setVotedFor1}
                                                  isChosen={isChosen.current} isItChosen={isItChosen} isFirstChosen={isFirstChosen.current > -1}
                                                  numberOfVoice={survey.sl1v} text={value}
                                />
                            );
                        }
                        case 2: {
                            return (
                                <SurveyInSiteLine key={index} index={index} IsMultipleChoice={Survey.is_multi_voices} setChosen={setVotedFor1}
                                                  isChosen={isChosen.current} isItChosen={isItChosen} isFirstChosen={isFirstChosen.current > -1}
                                                  numberOfVoice={survey.sl2v} text={value}
                                />
                            );
                        }
                        case 3: {
                            return (
                                <SurveyInSiteLine key={index} index={index} IsMultipleChoice={Survey.is_multi_voices} setChosen={setVotedFor1}
                                                  isChosen={isChosen.current} isItChosen={isItChosen} isFirstChosen={isFirstChosen.current > -1}
                                                  numberOfVoice={survey.sl3v} text={value}
                                />
                            );
                        }
                        case 4: {
                            return (
                                <SurveyInSiteLine key={index} index={index} IsMultipleChoice={Survey.is_multi_voices} setChosen={setVotedFor1}
                                                  isChosen={isChosen.current} isItChosen={isItChosen} isFirstChosen={isFirstChosen.current > -1}
                                                  numberOfVoice={survey.sl4v} text={value}
                                />
                            );
                        }
                        case 5: {
                            return (
                                <SurveyInSiteLine key={index} index={index} IsMultipleChoice={Survey.is_multi_voices} setChosen={setVotedFor1}
                                                  isChosen={isChosen.current} isItChosen={isItChosen} isFirstChosen={isFirstChosen.current > -1}
                                                  numberOfVoice={survey.sl5v} text={value}
                                />
                            );
                        }
                        case 6: {
                            return (
                                <SurveyInSiteLine key={index} index={index} IsMultipleChoice={Survey.is_multi_voices} setChosen={setVotedFor1}
                                                  isChosen={isChosen.current} isItChosen={isItChosen} isFirstChosen={isFirstChosen.current > -1}
                                                  numberOfVoice={survey.sl6v} text={value}
                                />
                            );
                        }
                        case 7: {
                            return (
                                <SurveyInSiteLine key={index} index={index} IsMultipleChoice={Survey.is_multi_voices} setChosen={setVotedFor1}
                                                  isChosen={isChosen.current} isItChosen={isItChosen} isFirstChosen={isFirstChosen.current > -1}
                                                  numberOfVoice={survey.sl7v} text={value}
                                />
                            );
                        }
                        case 8: {
                            return (
                                <SurveyInSiteLine key={index} index={index} IsMultipleChoice={Survey.is_multi_voices} setChosen={setVotedFor1}
                                                  isChosen={isChosen.current} isItChosen={isItChosen} isFirstChosen={isFirstChosen.current > -1}
                                                  numberOfVoice={survey.sl8v} text={value}
                                />
                            );
                        }
                        case 9: {
                            return (
                                <SurveyInSiteLine key={index} index={index} IsMultipleChoice={Survey.is_multi_voices} setChosen={setVotedFor1}
                                                  isChosen={isChosen.current} isItChosen={isItChosen} isFirstChosen={isFirstChosen.current > -1}
                                                  numberOfVoice={survey.sl9v} text={value}
                                />
                            );
                        }
                        default: {
                            return <></>
                        }
                    }
                })
            }
            {Survey.is_multi_voices
                ?
                    !isChosen.current
                        ?
                            <div style={{width: '100%', display: 'flex', justifyContent: 'center'}}>
                                <div
                                    style={{padding: '4px', background: 'rgba(255, 255, 255, .3)', borderRadius: '4px', cursor: 'pointer'}}
                                    onClick={
                                        () => {

                                            if ( (votedFor as number[]).length === 0) return;
                                            isChosen.current = true;
                                            VoteInSurveyFetch(id, votedFor as number[])
                                                .then( () => {
                                                    try{
                                                        setRender(!render)
                                                    } catch (e) {}
                                                });
                                        }
                                    }
                                >
                                    <div style={{userSelect: 'none'}}>
                                        Проголосовать
                                    </div>
                                </div>
                            </div>
                        :
                            <></>
                :
                    <></>
            }
        </div>
    );

});