import React, {memo, FC,  useEffect} from 'react';
import {TextBlock} from "./TextBlock";

interface Props {
    Question: string,
    Content: string | null,
    id: number
}

export const QuestionLine: FC<Props> = memo (({Question, Content, id}) => {
    
    useEffect( () => {
      if ( (document.getElementById('Question' + id) as HTMLDivElement) && (document.getElementById('Content' + id) as HTMLDivElement)) {
          if ( (document.getElementById('Question' + id) as HTMLDivElement)?.style.height > (document.getElementById('Content' + id) as HTMLDivElement)?.style.height) {
              (document.getElementById('Content' + id) as HTMLDivElement).style.height = (document.getElementById('Question' + id) as HTMLDivElement).style.height;
          }
      }
    }, [])

    if (Content == null || Content == '') {
        return (
            <></>
        );
    }

    return (
        <div style={{borderBottom: 'white solid 1px'}}>
            <TextBlock id={'Question' + id} Content={Question} Styles={{width:'170px',display: 'inline-block', borderRight: 'solid lightgrey 1px', marginRight: '10px', backgroundColor: 'rgba(0, 0, 0, 0)' }}/>
            <TextBlock id={'Content' + id} Content={Content} Styles={{width:'332px', display: 'inline-block', backgroundColor: 'rgba(0, 0, 0, 0)' }}/>
        </div>
    );

});