export interface FileObject {
	music_files: string[];
	video_files: string[];
	image_files: string[];
	other_files: string[];
}

export function SortFilesByExtension(...filenames: string[]): FileObject {
	const music_ext = ['.mp3', '.wav', '.flac'];
	const video_ext = ['.mp4', '.avi', '.mkv'];
	const image_ext = ['.jpg', '.png', '.bmp'];

	const music_files: string[] = [];
	const video_files: string[] = [];
	const image_files: string[] = [];
	const other_files: string[] = [];

	filenames.forEach((filename) => {
		const ext = filename.match(/\.[^\.]+$/)?.[0] ?? '';
		if (music_ext.includes(ext)) {
			music_files.push(filename);
		} else if (video_ext.includes(ext)) {
			video_files.push(filename);
		} else if (image_ext.includes(ext)) {
			image_files.push(filename);
		} else {
			other_files.push(filename);
		}
	});

	return {
		music_files,
		video_files,
		image_files,
		other_files,
	};
}