import React, {FC, memo} from 'react';
import styles from "../../../styles/MainPart.module.scss";
import {FriendStatusStore} from "../../../store/FriendStatusStore";

interface Props {
    IsFriendStore: 'none' | 'friend' | 'idol' | 'subscriber';
}

export const NotOwnerPanel: FC<Props> = memo(({IsFriendStore}) => {

    return (
        <div>
            {
                IsFriendStore === 'none'
                    ?
                        <div className={"PrevButtonInLeftColumn"} onClick={
                            () => {
                                FriendStatusStore.sendAFriendshipRequest();
                            }
                        }>
                            <div className={styles.PrevButtonInLeftColumn_Text}>Отправить запрос дружбы</div>
                        </div>
                    :
                    IsFriendStore === 'friend'
                        ?
                            <div className={"PrevButtonInLeftColumn"}>
                                <div className={styles.PrevButtonInLeftColumn_Text} onClick={
                                    () => {
                                        FriendStatusStore.removeFromFriendList();
                                    }
                                }>
                                    Удалить из друзей
                                </div>
                            </div>
                        :
                            IsFriendStore === 'idol'
                                ?
                                    <div className={"PrevButtonInLeftColumn"} onClick={
                                        () => {
                                            FriendStatusStore.takeBackFriendShipRequest();
                                        }
                                    }>
                                        <div className={styles.PrevButtonInLeftColumn_Text}>Отменить запрос дружбы</div>
                                    </div>
                                :
                                    <>
                                        <div className={"PrevButtonInLeftColumn"} onClick={() => {
                                            FriendStatusStore.acceptTheInvitation();
                                        }}>
                                            <div className={styles.PrevButtonInLeftColumn_Text}>
                                                Добавить в друзья
                                            </div>
                                        </div>
                                    </>
            }
            {
                /*
                <div className={"PrevButtonInLeftColumn"}>
                    <div className={styles.PrevButtonInLeftColumn_Text}>
                        Отправить сообщение
                    </div>
                </div>
                * */
            }
        </div>
    );
});